import React, { useEffect, useState } from "react";
import clsx from "clsx";
import { useSelector, useDispatch, shallowEqual } from "react-redux";
import { eCalcForms } from "../../../lib";
// import { AddIcon } from "../../../themes";
import {
  eCalcOnChange,
  addResults,
  removeResults,
} from "../../../actions/eCalcForms";
import { MissingInputsMsg } from "../components";
import { formatDollar, parseToNumber } from "../helpers";
import {
  calcFirstYearCashOnCash,
  calculateFreeCashFlow,
} from "./CashOnCash.logic";
import { useStyles } from "./CashOnCash.styles";
import { TextField, InputAdornment } from "@material-ui/core";

export function CashOnCash({ storeSliceName, persist }) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const cashOnCashValues = useSelector(
    (state) => state[storeSliceName][eCalcForms.CASH_ON_CASH],
    shallowEqual
  );
  const equityNeededValues = useSelector(
    (state) => state[storeSliceName][eCalcForms.EQUITY_NEEDED],
    shallowEqual
  );
  const mortgageDcsr = useSelector(
    (state) => state[storeSliceName][eCalcForms.MORTGAGE_AND_DSCR],
    shallowEqual
  );
  const first_year_noi = useSelector(
    (state) => state[storeSliceName][eCalcForms.CAP_RATE].first_year_noi
  );

  const { equity_needed, first_year_cash_flow } = cashOnCashValues || {};
  const { total_capitalization, loan_amount_dollar } = equityNeededValues || {};
  const { interest_rate, amortization_years, io_years } = mortgageDcsr || {};

  const [showInputsMissingMsg, setShowInputsMissingMsg] = useState(false);

  useEffect(() => {
    let results = "";
    if (total_capitalization && loan_amount_dollar) {
      results = formatDollar(
        parseToNumber(total_capitalization) - parseToNumber(loan_amount_dollar)
      );
    }
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.CASH_ON_CASH,
        name: "equity_needed",
        value: results,
        persist,
      })
    );
  }, [total_capitalization, loan_amount_dollar, dispatch, persist]);

  useEffect(() => {
    if (
      !total_capitalization ||
      !loan_amount_dollar ||
      !interest_rate ||
      !amortization_years
    ) {
      setShowInputsMissingMsg(true);
    } else {
      setShowInputsMissingMsg(false);
    }
  }, [
    total_capitalization,
    loan_amount_dollar,
    interest_rate,
    amortization_years,
  ]);

  useEffect(() => {
    let resultValue = "";
    if (first_year_noi) {
      const netCashFlow = calculateFreeCashFlow({
        first_year_noi,
        loan_amount_dollar,
        interest_rate,
        amortization_years,
        io_years,
      });
      resultValue = netCashFlow.results;
      dispatch(
        addResults({
          name: "first_year_cash_flow",
          value: netCashFlow,
          persist,
        })
      );
    } else {
      dispatch(removeResults({ name: "first_year_cash_flow", persist }));
    }
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.CASH_ON_CASH,
        name: "first_year_cash_flow",
        value: resultValue,
        persist,
      })
    );
  }, [
    first_year_noi,
    loan_amount_dollar,
    interest_rate,
    amortization_years,
    io_years,
    dispatch,
    persist,
  ]);

  useEffect(() => {
    if (first_year_noi && equity_needed) {
      const cashOncashReturn = calcFirstYearCashOnCash({
        first_year_noi,
        loan_amount_dollar,
        interest_rate,
        amortization_years,
        equity_needed,
        io_years,
      });
      dispatch(
        addResults({
          name: "cash_on_cash_return",
          value: cashOncashReturn,
          persist,
        })
      );
    } else {
      dispatch(removeResults({ name: "cash_on_cash_return", persist }));
    }
  }, [
    first_year_noi,
    loan_amount_dollar,
    interest_rate,
    amortization_years,
    equity_needed,
    io_years,
    dispatch,
    persist,
  ]);

  return (
    <div className={classes.calcContainer} style={{ width: "100%" }}>
      {showInputsMissingMsg && (
        <MissingInputsMsg calculator="Cash On Cash" persist={persist} />
      )}
      <TextField
        className={clsx(classes.margin, classes.fullWidthInput)}
        label="Equity Needed:"
        variant="outlined"
        value={equity_needed}
        onChange={() => {}}
        name="equity_needed"
        fullWidth
        disabled
        InputProps={{
          classes: { input: classes.input },
          startAdornment: (
            <InputAdornment
              position="start"
              classes={{ root: classes.inputAdornment }}
            >
              $
            </InputAdornment>
          ),
        }}
        InputLabelProps={{
          classes: {
            root: clsx(classes.inputLabel, classes.fullWidthLabel),
            shrink: "fullInputShrink",
          },
        }}
      />
      <TextField
        className={clsx(classes.margin, classes.fullWidthInput)}
        label="First Year Net Cash Flow (NCF):"
        variant="outlined"
        value={formatDollar(first_year_cash_flow)}
        onChange={() => {}}
        name="first_year_cash_flow"
        fullWidth
        disabled
        InputProps={{
          classes: { input: classes.input },
          startAdornment: (
            <InputAdornment
              position="start"
              classes={{ root: classes.inputAdornment }}
            >
              $
            </InputAdornment>
          ),
        }}
        InputLabelProps={{
          classes: {
            root: clsx(
              classes.inputLabel,
              classes.longLabel,
              classes.fullWidthLabel
            ),
            shrink: "fullInputShrink",
          },
        }}
      />
    </div>
  );
}
