export function calcTotalLoanAndBlendedRate(loans) {
  var totalLoan = 0;
  var blendedRate = 0;
  if (Array.isArray(loans)) {
    const loansList = loans.map(loan => ({
      amount: loan.amount ? parseFloat(`${loan.amount}`.replace(/,/g, "")) : 0,
      rate: loan.rate ? parseFloat(`${loan.rate}`.replace(/,/g, "")) : 0
    }));
    totalLoan =
      loansList && loansList.map(l => l.amount).reduce((a, b) => a + b);
    blendedRate =
      loansList.reduce(
        (prev, current) => prev + current.amount * (current.rate / 100),
        0
      ) / totalLoan;
  }
  return {
    totalLoan,
    blendedRate: blendedRate ? +parseFloat(blendedRate * 100).toFixed(3) : 0
  };
}
