import { addResults, removeResults } from "../../../actions/eCalcForms";
import { Finance } from "financejs";
const finance = new Finance();

const calculateStats = (data, dispatch, persist) => {
  const {
    lpDollars,
    gpDollars,
    lpPercent,
    gpPercent,
    hurdles,
    netCashFlow,
    capitalEventNetCashFlow,
  } = data;
  const lpStats = [];
  const gpStats = [];
  const NCF = capitalEventNetCashFlow ? capitalEventNetCashFlow : netCashFlow;
  const currentCashFlows = [...NCF.slice(0, -1).map((cf) => cf.amount)];
  hurdles.forEach((hurdle, hurdleIndex) => {
    let { endPercent, promotePercent } = hurdle;
    endPercent = !endPercent ? 0 : divide(endPercent, 100);
    if (!promotePercent) promotePercent = 0;
    let lpBalance = +lpDollars;
    let gpBalance = +gpDollars;
    const lpAdjustedPercent = multiply(
      1 - divide(promotePercent, 100),
      divide(lpPercent, 100)
    );
    const gpAdjustedPercent = add(
      divide(promotePercent, 100),
      multiply(1 - divide(promotePercent, 100), divide(gpPercent, 100))
    );
    for (let i = 0; i < currentCashFlows.length; i++) {
      let gpDistribution;
      let lpDistribution;
      if (hurdleIndex < hurdles.length - 1 && hurdles.length > 1) {
        //////// LP INFO ////////
        const lpRequiredRet = multiply(endPercent, lpBalance);
        let firstEq = add(lpRequiredRet, lpBalance);
        if (hurdleIndex > 0) firstEq = subtract(firstEq, lpStats[i]);
        lpDistribution = Math.min(
          firstEq,
          multiply(lpAdjustedPercent, currentCashFlows[i])
        );
        lpBalance = subtract(
          add(lpBalance, lpRequiredRet),
          lpStats[i] ? lpStats[i] : 0,
          lpDistribution
        );
        //////// GP INFO ///////
        if (hurdleIndex === 0) {
          /// get GP stats on first hurdle
          const gpRequiredRet = multiply(endPercent, gpBalance);
          gpDistribution = Math.min(
            add(gpRequiredRet, gpBalance),
            multiply(gpAdjustedPercent, currentCashFlows[i])
          );
          gpBalance = subtract(add(gpBalance, gpRequiredRet), gpDistribution);
        } else {
          gpDistribution = multiply(
            divide(lpDistribution, lpAdjustedPercent),
            gpAdjustedPercent
          );
        }
      } else {
        lpDistribution = multiply(currentCashFlows[i], lpAdjustedPercent);
        gpDistribution = multiply(currentCashFlows[i], gpAdjustedPercent);
      }
      lpStats[i] = lpStats[i]
        ? add(lpStats[i], lpDistribution)
        : lpDistribution;
      gpStats[i] = gpStats[i]
        ? add(gpStats[i], gpDistribution)
        : gpDistribution;
      // update available cash
      currentCashFlows[i] = subtract(
        currentCashFlows[i],
        lpDistribution,
        gpDistribution
      );
    }
  });
  try {
    const gpIrr = finance.IRR(-gpDollars, ...gpStats);
    const lpIrr = finance.IRR(-lpDollars, ...lpStats);

    const lpTotal = lpStats.reduce((a, b) => add(a, b));
    const gpTotal = gpStats.reduce((a, b) => add(a, b));

    dispatch(
      addResults({
        name: "gp_stats",
        value: {
          label: "Sponsor (GP)",
          results: [
            {
              name: "Total Contribution",
              value: gpDollars,
              displayFormat: "dollar",
            },
            {
              name: "IRR",
              value: gpIrr,
              displayFormat: "percent",
            },
            {
              name: "Total Distribution",
              value: gpTotal,
              displayFormat: "dollar",
            },
          ],
        },
        persist,
      })
    );
    dispatch(
      addResults({
        name: "lp_stats",
        value: {
          label: "Sponsor (LP)",
          results: [
            {
              name: "Total Contribution",
              value: lpDollars,
              displayFormat: "dollar",
            },
            {
              name: "IRR",
              value: lpIrr,
              displayFormat: "percent",
            },
            {
              name: "Total Distribution",
              value: lpTotal,
              displayFormat: "dollar",
            },
          ],
        },
        persist,
      })
    );
  } catch (error) {
    dispatch(removeResults({ name: "lp_stats", persist }));
    dispatch(removeResults({ name: "gp_stats", persist }));
  }
};

export { calculateStats };

function add(num1, num2) {
  return (+num1 * 10 + +num2 * 10) / 10;
}

function subtract(num1, num2, num3 = 0) {
  return Number(((+num1 * 10 - +num2 * 10 - +num3 * 10) / 10).toFixed(8));
}

function multiply(num1, num2) {
  return Number((+num1 * +num2).toFixed(8));
}

function divide(num1, num2) {
  return Number((+num1 / +num2).toFixed(8));
}
