import React, { useState, useCallback, useEffect } from "react";
import clsx from "clsx";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import {
  // Divider,
  TextField,
  InputAdornment,
} from "@material-ui/core";
import mixpanel from "mixpanel-browser";
import { eCalcForms } from "../../../lib";
import {
  setIsDirty,
  eCalcOnChange,
  setActiveCalculator,
  addUserEntryMethod,
  addResults,
  removeResults,
  clearBlendedRateItemization,
} from "../../../actions/eCalcForms";
import { SignalCellularAltIcon, ListIcon } from "../../../themes";
import {
  formatDollar,
  formatPercent,
  entryMethodNames,
  entryMethods,
  noDecimal,
  parseToNumber,
  // numberToPercent,
} from "../helpers";
import {
  BlendedRateItemization,
  MaxBankWillLend,
  MissingNoiMsg,
  RatesModal,
  AmortizationScheduleModal,
} from "../components";
import {
  calcMortgageDcsrPostIO,
  calcMortgageDcsrDuringIO,
  calcBalanceAtMaturity,
  calcDcsrPostIo,
  calcDcsrduringIo,
  calcAmortizationSchedule,
} from "./MortgageDscr.logic";
import { calcTotalLoanAndBlendedRate } from "../components/BlendedRateItemization.logic";
import { useStyles } from "./MortgageDscr.styles";
import { showNotification } from "../../../actions/uiActions";
import { Notification } from "../../common/Notification";

export function MortgageDscr({ storeSliceName, persist }) {
  const classes = useStyles();
  const dispatch = useDispatch();

  const dirty = useSelector((state) => state[storeSliceName].dirty);
  const inputValues = useSelector(
    (state) => state[storeSliceName][eCalcForms.MORTGAGE_AND_DSCR],
    shallowEqual
  );
  const total_capitalization = useSelector(
    (state) =>
      state[storeSliceName][eCalcForms.EQUITY_NEEDED].total_capitalization
  );
  const loan_amount_dollar = useSelector(
    (state) =>
      state[storeSliceName][eCalcForms.EQUITY_NEEDED].loan_amount_dollar
  );
  const loans = useSelector(
    (state) => state[storeSliceName][eCalcForms.EQUITY_NEEDED].loans
  );
  const first_year_noi = useSelector(
    (state) => state[storeSliceName][eCalcForms.CAP_RATE].first_year_noi
  );
  const { interest_rate, init_loan_term_yrs, io_years, amortization_years } =
    inputValues || {};

  const purchase_price = useSelector(
    (state) => state[storeSliceName][eCalcForms.CAP_RATE].purchase_price
  );

  const mortgage_post_io = useSelector(
    (state) => state[storeSliceName].resultsValues.mortgage_post_io,
    shallowEqual
  );

  const mortgage_during_io = useSelector(
    (state) => state[storeSliceName].resultsValues.mortgage_during_io,
    shallowEqual
  );

  const userEntryMethods = useSelector(
    (state) => state[storeSliceName].userEntryMethods,
    shallowEqual
  );
  const { /* closingCost, loanAmount, */ loanAmountOf } = userEntryMethods;

  const [showIO, setShowIO] = useState(!!io_years);
  const [showMaxBank, setShowMaxBank] = useState(false);
  const [showBlendedRateItemization, setShowBlendedRateItemization] = useState(
    false
  );
  const [showRatesModal, setShowRatesModal] = useState(false);

  const openRatesModal = useCallback(() => {
    mixpanel.track("Rates Modal Opened");
    setShowRatesModal(true);
  }, []);

  const closeRatesModal = useCallback(() => {
    setShowRatesModal(false);
  }, []);

  function handleInputChange(event) {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;
    const intValue = parseFloat(value.replace(/[^0-9.]/g, "")); //Strips of non-numeric characters besides decimal
    dispatch(
      eCalcOnChange({
        // the loan_amount field is a duplicate of the equity needed calculator
        calculator:
          name !== "loan_amount_dollar"
            ? eCalcForms.MORTGAGE_AND_DSCR
            : eCalcForms.EQUITY_NEEDED,
        name,
        value: formatInput(name, value),
        persist,
      })
    );
    // need to know which calculator the user is entering for mobile slider
    dispatch(setActiveCalculator({ name: "mortgage", persist }));
    typeof syncFields[name] === "function" &&
      syncFields[name]({
        value: intValue,
        dispatch,
        state: { ...inputValues, loan_amount_dollar },
        meta: {},
        persist,
      });
    if (!dirty) {
      dispatch(setIsDirty(persist));
    }
  }

  useEffect(() => {
    if (loan_amount_dollar && interest_rate && amortization_years) {
      const results = calcMortgageDcsrPostIO({
        loan_amount_dollar,
        interest_rate,
        amortization_years,
        io_years,
      });
      dispatch(
        addResults({
          name: "mortgage_post_io",
          value: results,
          persist,
        })
      );
      if (io_years) {
        const ioResults = calcMortgageDcsrDuringIO({
          loan_amount_dollar,
          interest_rate,
        });
        dispatch(
          addResults({
            name: "mortgage_during_io",
            value: ioResults,
            persist,
          })
        );
      }
    } else {
      dispatch(removeResults({ name: "mortgage_post_io", persist }));
    }
  }, [
    loan_amount_dollar,
    interest_rate,
    amortization_years,
    io_years,
    dispatch,
    persist,
  ]);

  useEffect(() => {
    if (mortgage_post_io?.results || mortgage_during_io?.results) {
      const amortizationSchedule = calcAmortizationSchedule({
        mortgage_during_io,
        mortgage_post_io,
        io_years,
        loan_amount_dollar,
        interest_rate,
        init_loan_term_yrs,
        amortization_years,
      });
      dispatch(
        eCalcOnChange({
          // the loan_amount field is a duplicate of the equity needed calculator
          calculator: eCalcForms.MORTGAGE_AND_DSCR,
          name: "amortization_schedule",
          value: amortizationSchedule,
          persist,
        })
      );
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [mortgage_during_io, mortgage_post_io, init_loan_term_yrs, persist]);

  useEffect(() => {
    if (
      loan_amount_dollar &&
      interest_rate &&
      amortization_years &&
      init_loan_term_yrs
    ) {
      const results = calcBalanceAtMaturity({
        loan_amount_dollar,
        interest_rate,
        amortization_years,
        init_loan_term_yrs,
        io_years,
      });
      dispatch(
        addResults({
          name: "balance_at_maturity",
          value: results,
          persist,
        })
      );
    } else {
      dispatch(removeResults({ name: "balance_at_maturity", persist }));
    }
  }, [
    loan_amount_dollar,
    interest_rate,
    amortization_years,
    init_loan_term_yrs,
    io_years,
    dispatch,
    persist,
  ]);

  useEffect(() => {
    if (
      loan_amount_dollar &&
      interest_rate &&
      amortization_years &&
      first_year_noi
    ) {
      if (io_years) {
        const ioResults = calcDcsrduringIo({
          loan_amount_dollar,
          interest_rate,
          amortization_years,
          first_year_noi,
          io_years,
        });
        // we only show one results
        dispatch(removeResults({ name: "dscr_post_io", persist }));
        dispatch(
          addResults({
            name: "dscr_during_post_io",
            value: ioResults,
            persist,
          })
        );
      } else {
        const results = calcDcsrPostIo({
          loan_amount_dollar,
          interest_rate,
          amortization_years,
          first_year_noi,
        });
        dispatch(
          removeResults({
            name: "dscr_during_post_io",
            persist,
          })
        );
        dispatch(
          addResults({
            name: "dscr_post_io",
            value: results,
            persist,
          })
        );
      }
    } else {
      dispatch(removeResults({ name: "dscr_post_io", persist }));
      dispatch(removeResults({ name: "dscr_during_post_io", persist }));
    }
  }, [
    loan_amount_dollar,
    interest_rate,
    first_year_noi,
    amortization_years,
    io_years,
    dispatch,
    persist,
  ]);

  const closeIO = useCallback(() => {
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.MORTGAGE_AND_DSCR,
        name: "io_years",
        value: "",
        persist,
      })
    );
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.MORTGAGE_AND_DSCR,
        name: "bank_max_loan_during_io",
        value: "",
        persist,
      })
    );
    dispatch(removeResults({ name: "mortgage_during_io", persist }));
    dispatch(removeResults({ name: "dscr_during_post_io", persist }));
    setShowIO(false);
  }, [dispatch, persist]);

  const [loansList, setLoans] = useState(loans);
  const [totalLoan, setTotalLoan] = useState(0);
  const [totalLoanPercentage, setTotalLoanPercentage] = useState(0);
  const [blendedRate, setBlendedRate] = useState(0);
  const [
    showAmortizationScheduleModal,
    setShowAmortizationScheduleModal,
  ] = useState(false);

  useEffect(() => {
    const results = calcTotalLoanAndBlendedRate(loansList);
    setTotalLoan(formatDollar(results.totalLoan));
    setTotalLoanPercentage(
      loanAmountOf === "totalCapitalization"
        ? total_capitalization
          ? formatPercent(
              parseToNumber(results.totalLoan) /
                parseToNumber(total_capitalization)
            )
          : ""
        : purchase_price
        ? formatPercent(
            parseToNumber(results.totalLoan) / parseToNumber(purchase_price)
          )
        : ""
    );
    setBlendedRate(results.blendedRate);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loansList]);

  useEffect(() => {
    setLoans(loans);
  }, [loans]);

  const openBlendedRateItemization = useCallback(() => {
    mixpanel.track("ecalc_popup", { popup: "Blended Rates screen" });
    setShowBlendedRateItemization(true);
  }, []);

  function blendedRateItemizationClose() {
    setLoans(loans);
    setShowBlendedRateItemization(false);
  }

  const validateAmortScheduleFields = ({ fields = [] }) => {
    let invalidFields = [];
    fields.map((field) => {
      return !field.value ? invalidFields.push(field.name) : null;
    });

    if (invalidFields.length) {
      //separate each invalid field with comma. last field should have 'and' instead
      let errorMessage = invalidFields
        .join(", ")
        .replace(/,(?!.*,)/gim, " and");
      errorMessage += ` ${invalidFields.length > 1 ? "are" : "is"} required!`;
      dispatch(showNotification({ message: errorMessage }));
      return false;
    } else {
      return true;
    }
  };

  const isItemized = Array.isArray(loans) && loans.some((loan) => loan.amount);
  return (
    <div className={classes.calcContainer}>
      <TextField
        inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
        className={clsx(classes.margin, classes.fullWidthInput)}
        label={isItemized ? "Total Loan Amount: " : "Loan Amount:"}
        disabled={!!isItemized}
        variant="outlined"
        value={isItemized ? totalLoan : loan_amount_dollar}
        onChange={handleInputChange}
        name="loan_amount_dollar"
        fullWidth
        InputProps={{
          classes: { input: classes.input },
          startAdornment: (
            <InputAdornment
              position="start"
              classes={{ root: classes.inputAdornment }}
            >
              $
            </InputAdornment>
          ),
          endAdornment: (
            <InputAdornment position="end">
              <ListIcon
                className={classes.listIcon}
                onClick={openBlendedRateItemization}
              />
            </InputAdornment>
          ),
        }}
        InputLabelProps={{
          classes: {
            root: clsx(classes.inputLabel, classes.fullWidthLabel),
            shrink: "fullInputShrink",
          },
        }}
      />
      <div className={classes.lineInputsContainer}>
        <TextField
          inputProps={{ inputMode: "decimal" }}
          className={clsx(classes.margin, classes.smallInput)}
          label={
            <span style={{ display: "flex" }}>
              {isItemized ? "Blended Initial Rate" : "Interest Rate:"}
              <SignalCellularAltIcon
                className={classes.rateIcon}
                onClick={openRatesModal}
              />
            </span>
          }
          disabled={!!isItemized}
          variant="outlined"
          value={isItemized ? blendedRate : interest_rate}
          onChange={handleInputChange}
          name="interest_rate"
          InputProps={{
            endAdornment: (
              <InputAdornment
                position="end"
                classes={{ root: classes.inputAdornment }}
              >
                %
              </InputAdornment>
            ),
            classes: { input: classes.inputWithPadding },
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
            shrink: true,
          }}
        />
        <span className={classes.inputsDivider} style={{ height: 0 }} />
        <TextField
          inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Initial Loan Term (YRS):"
          variant="outlined"
          value={init_loan_term_yrs}
          onChange={handleInputChange}
          name="init_loan_term_yrs"
          InputProps={{
            classes: { input: classes.inputWithPadding },
          }}
          InputLabelProps={{
            classes: {
              root: classes.inputLabel,
              shrink: "shrink",
            },
            shrink: true,
          }}
        />
      </div>
      <div
        className={classes.toggleLink}
        onClick={showIO ? closeIO : () => setShowIO(true)}
        style={{ marginBottom: showIO ? -33 : 10 }}
      >
        {showIO ? "Remove I/O (Interest Only)" : "Add I/O (Interest Only)"}
      </div>
      {showIO && (
        <div className={classes.lineInputsContainer}>
          {/* For spacing */}
          <div className={clsx(classes.margin, classes.smallInput)} />
          <span className={classes.inputsDivider} style={{ height: 0 }} />
          <TextField
            inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
            className={clsx(classes.margin, classes.smallInput)}
            label="I/O (interest only) Years:"
            variant="outlined"
            value={io_years}
            onChange={handleInputChange}
            name="io_years"
            InputProps={{
              classes: { input: classes.inputWithPadding },
            }}
            InputLabelProps={{
              classes: {
                root: classes.inputLabel,
                shrink: "shrink",
              },
              shrink: true,
            }}
          />
        </div>
      )}
      <TextField
        inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
        className={clsx(classes.margin, classes.fullWidthInput)}
        label={
          showIO ? "Amortization after I/O in Years:" : "Amortization in Years:"
        }
        variant="outlined"
        value={amortization_years}
        onChange={handleInputChange}
        name="amortization_years"
        fullWidth
        InputProps={{
          classes: { input: classes.inputWithPadding },
        }}
        InputLabelProps={{
          classes: {
            root: clsx(
              classes.inputLabel,
              classes.longLabel,
              classes.fullWidthLabel
            ),
            shrink: "fullInputShrink",
          },
          shrink: true,
        }}
      />
      <div
        className={classes.toggleLink}
        onClick={() => {
          if (
            validateAmortScheduleFields({
              fields: [
                { name: "Loan Amount", value: loan_amount_dollar },
                { name: "Interest Rate", value: interest_rate },
                { name: "Amortization Years", value: amortization_years },
              ],
            })
          ) {
            mixpanel.track("amortization modal opened");
            setShowAmortizationScheduleModal(true);
          }
        }}
      >
        View Amortization Schedule
      </div>

      <Notification />

      {first_year_noi ? (
        <div
          className={classes.toggleLink}
          onClick={() => setShowMaxBank(true)}
        >
          Figure out Max Bank will Lend
        </div>
      ) : (
        <MissingNoiMsg />
      )}
      {showMaxBank && (
        <MaxBankWillLend
          open={showMaxBank}
          handleClose={() => setShowMaxBank(false)}
          storeSliceName={storeSliceName}
          persist={persist}
        />
      )}

      {showBlendedRateItemization && (
        <BlendedRateItemization
          loans={loansList}
          blendedRate={blendedRate}
          totalLoan={totalLoan}
          totalLoanPercentage={totalLoanPercentage}
          setLoans={(loans) => setLoans(loans)}
          onClear={() => dispatch(clearBlendedRateItemization(persist))}
          onClose={() => blendedRateItemizationClose()}
          persist={persist}
        />
      )}

      {showAmortizationScheduleModal && (
        <AmortizationScheduleModal
          open={showAmortizationScheduleModal}
          onClose={() => setShowAmortizationScheduleModal(false)}
          onOpen={() => {
            mixpanel.track("amortization modal opened");
            setShowAmortizationScheduleModal(true);
          }}
          storeSliceName={storeSliceName}
        />
      )}
      <RatesModal
        open={showRatesModal}
        handleOpen={openRatesModal}
        handleClose={closeRatesModal}
      />
    </div>
  );
}

const syncFields = {
  loan_amount_dollar({ value, dispatch, state, meta, persist }) {
    dispatch(
      addUserEntryMethod({
        name: entryMethodNames.LOAN_AMOUNT,
        value: entryMethods.DOLLAR_AMOUNT,
        persist,
      })
    );
  },
};

function formatInput(name, value) {
  switch (name) {
    case "loan_amount_dollar":
    case "init_loan_term_yrs":
    case "amortization_years":
      return noDecimal(parseToNumber(value));
    default:
      return value;
  }
}
