import { parseToNumber } from "../helpers";

export function calcWeightedAvg(data, holding_period_yrs) {
  if (data.length) {
    const startingVal = data[0]; // ? parseFloat(`${data[0]}`.replace(",", "")) : 0;
    const endingVal = data[data.length - 1]; // ? parseFloat(`${data[data.length - 1]}`.replace(",", "")) : 0;
    const avg = +parseFloat(
      (Math.pow(endingVal / startingVal, 1 / holding_period_yrs) - 1) * 100
    ).toFixed(3);
    return avg || 0;
  }
}

