export function calcNOIWithoutIrrSchedule({
  holding_period_yrs,
  projected_gross_income_rate,
  stabilized_vacancy,
  projected_annual_expense,
  annual_expenses,
  potential_annual_gross_income,
}) {
  const totalExpenses = [
    annual_expenses,
    ...new Array(holding_period_yrs).fill(projected_annual_expense / 100 + 1),
  ].reduce((a, b) => a * b);
  const totalPgi = [
    potential_annual_gross_income,
    ...new Array(holding_period_yrs).fill(
      projected_gross_income_rate / 100 + 1
    ),
  ].reduce((a, b) => a * b);
  const startingVal =
    potential_annual_gross_income -
    potential_annual_gross_income * (stabilized_vacancy / 100) -
    annual_expenses;
  const endingVal =
    totalPgi - totalPgi * (stabilized_vacancy / 100) - totalExpenses;
  return +parseFloat(
    (Math.pow(endingVal / startingVal, 1 / holding_period_yrs) - 1) * 100
  ).toFixed(3);
}

export function calcEquityMultiple({
  grand_total_distribution,
  equity_needed,
}) {
  const results = +parseFloat(
    parseToNumber(grand_total_distribution) / parseToNumber(equity_needed)
  ).toFixed(3);
  return {
    label: "Equity Multiple",
    results,
    displayFormat: "times",
  };
}
