import { parseToNumber } from "../helpers";

export function calcEquityNeeded({ loan_amount_dollar, total_capitalization }) {
  const results =
    parseToNumber(total_capitalization) - parseToNumber(loan_amount_dollar);
  return { label: "Equity Needed", results, displayFormat: "dollar" };
}

export function calcDebtYield({ loan_amount_dollar, first_year_noi }) {
  const results = +parseFloat(
    (parseToNumber(first_year_noi) / parseToNumber(loan_amount_dollar)) * 100
  ).toFixed(2);
  return {
    label: "Debt Yield",
    results,
    displayFormat: "percent",
    showDebtYield: true
  };
}
