import React, {
  useState,
  useEffect,
  useCallback,
  useRef,
  Fragment,
} from "react";
import clsx from "clsx";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import _snakeCase from "lodash/snakeCase";
import _flatMap from "lodash/flatMap";
import _sum from "lodash/sum";
import _max from "lodash/max";
import { Divider, List, ListItem, Grid } from "@material-ui/core";
import { eCalcForms, renameObjKey } from "../../../lib";
import { ItemizationLayout } from "./Itemization";
import { EditableItemizationText } from "../components";
import {
  updateItemization,
  eCalcOnMultiChange,
  setActiveCalculator,
  addUserItemizationEntryMethod,
  deleteUserItemizationEntryMethod,
  updateItemizationPercentOf,
  deleteClosingCostPercentOf,
} from "../../../actions/eCalcForms";
import {
  entryMethods,
  entryMethodNames,
  noDecimal,
  parseToNumber,
  numberToPercent,
  formatPercent,
} from "../helpers";
import { useStyles } from "./closingCosts.styles";
const PURCHASE_PRICE = "purchase_price";
const LOAN_AMOUNT = "loan_amount_dollar";

export function ClosingCostItemized({
  open,
  handleClose,
  storeSliceName,
  persist,
}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const isInitialState = useRef(true);

  const itemizationValues = useSelector(
    (state) => state[storeSliceName].itemizations?.[eCalcForms.CLOSING_COSTS],
    shallowEqual
  );

  const userItemizationEntryMethods = useSelector(
    (state) =>
      state[storeSliceName].userItemizationEntryMethods?.[
        entryMethodNames.CLOSING_COST_ITEMIZED
      ],
    shallowEqual
  );
  const percentOf = useSelector(
    (state) =>
      state[storeSliceName].itemizationsPercentOf?.[
        entryMethodNames.CLOSING_COST_ITEMIZED
      ],
    shallowEqual
  );
  const purchase_price = useSelector(
    (state) => state[storeSliceName][eCalcForms.CAP_RATE].purchase_price
  );
  const loan_amount_dollar = useSelector(
    (state) =>
      state[storeSliceName][eCalcForms.EQUITY_NEEDED].loan_amount_dollar
  );

  const percentOfAmountObj = {
    purchase_price,
    loan_amount_dollar,
  };

  const [initialValues, setInitialValues] = useState();
  const [itemsToHide, setItemsToHide] = useState([]);
  const [totals, setTotals] = useState();
  // we need to track how many fields the user added
  const [totalAddedFields, setTotalAddedFields] = useState(0);

  const newValueRef = useCallback((node) => {
    if (node !== null) {
      node.scrollIntoView({
        behavior: "smooth",
        block: "start",
      });
    }
  }, []);

  useEffect(() => {
    const addedFieldsArr = Object.keys(itemizationValues).reduce(
      (acc, cur) => {
        if (cur.includes("new_cost")) {
          // get the number at the end of the key
          const split = cur.split("_");
          const keyNumber = split[split.length - 1];
          if (!isNaN(keyNumber)) {
            acc.push(+keyNumber);
          }
        }
        return acc;
      },
      [0]
    );
    setTotalAddedFields(_max(addedFieldsArr));
  }, [itemizationValues]);

  useEffect(() => {
    if (isInitialState.current) {
      // we need to store the initial state so we can reset
      setInitialValues(itemizationValues);
      isInitialState.current = false;
    }
    const showPercentValue = !!purchase_price;
    const {
      [entryMethods.DOLLAR_AMOUNT]: dollar,
      [entryMethods.PERCENT_AMOUNT]: percent,
    } = getTotalAmounts({
      itemizationValues,
      purchasePrice: purchase_price,
      canShowPercent: showPercentValue,
    });
    if (showPercentValue) {
      setTotals([
        { label: "Total Closing Costs $:", value: dollar, display: "dollar" },
        { label: "Total Closing Costs %:", value: percent, display: "percent" },
      ]);
    } else {
      setTotals({ label: "Total Closing Costs $:", value: dollar });
    }
  }, [itemizationValues, loan_amount_dollar, purchase_price]);

  const handleKeysBlur = useCallback(
    (e) => {
      const { name, value } = e.target;
      const formatted = value?.indexOf("/") === -1 ? _snakeCase(value) : value;
      const newValues = renameObjKey({
        object: itemizationValues,
        oldKey: name,
        newKey: formatted,
      });
      dispatch(
        updateItemization({
          name: eCalcForms.CLOSING_COSTS,
          value: newValues,
          persist,
        })
      );
      // We need to update the key in the `percentOf` object
      dispatch(
        updateItemizationPercentOf({
          itemizationName: entryMethodNames.CLOSING_COST_ITEMIZED,
          item: formatted,
          value: percentOf[name],
          persist,
        })
      );
      // We need to update the key in the `userItemizationEntryMethods` object
      dispatch(
        addUserItemizationEntryMethod({
          itemizationName: entryMethodNames.CLOSING_COST_ITEMIZED,
          item: formatted,
          value: userItemizationEntryMethods[name],
          persist,
        })
      );
      // delete old keys
      dispatch(
        deleteUserItemizationEntryMethod({
          itemizationName: entryMethodNames.CLOSING_COST_ITEMIZED,
          item: name,
          persist,
        })
      );
      dispatch(
        deleteClosingCostPercentOf({
          itemizationName: entryMethodNames.CLOSING_COST_ITEMIZED,
          item: name,
          persist,
        })
      );
    },
    [
      dispatch,
      persist,
      itemizationValues,
      percentOf,
      userItemizationEntryMethods,
    ]
  );

  const handleValuesBlur = useCallback(
    (e, nestedKey) => {
      const { name, value } = e.target;
      if (
        parseToNumber(value) ===
        parseToNumber(itemizationValues[name]?.[nestedKey])
      ) {
        return;
      }
      const canBeSynced = itemizationValues[name].hasOwnProperty("percent");
      const percentOfAmount = percentOfAmountObj[percentOf[name]];
      const syncedFields = syncFields({
        nestedKey,
        value,
        canBeSynced,
        percentOfAmount,
      });
      const newValues = {
        ...itemizationValues,
        [name]: { ...itemizationValues[name], ...syncedFields },
      };
      const {
        [entryMethods.DOLLAR_AMOUNT]: dollarAmt,
        [entryMethods.PERCENT_AMOUNT]: percentAmt,
      } = getTotalAmounts({
        itemizationValues: newValues,
        purchasePrice: purchase_price,
        canShowPercent: !!purchase_price,
      });
      dispatch(
        eCalcOnMultiChange({
          calculator: eCalcForms.EQUITY_NEEDED,
          valuesObj: {
            closing_costs_dollar: dollarAmt,
            closing_costs_percent: percentAmt,
          },
          persist,
        })
      );
      dispatch(
        updateItemization({
          name: eCalcForms.CLOSING_COSTS,
          value: newValues,
          persist,
        })
      );
      if (canBeSynced) {
        dispatch(
          addUserItemizationEntryMethod({
            itemizationName: entryMethodNames.CLOSING_COST_ITEMIZED,
            item: name,
            value:
              nestedKey === entryMethods.DOLLAR_AMOUNT
                ? entryMethods.DOLLAR_AMOUNT
                : entryMethods.PERCENT_AMOUNT,
            persist,
          })
        );
        dispatch(
          updateItemizationPercentOf({
            itemizationName: entryMethodNames.CLOSING_COST_ITEMIZED,
            item: name,
            value: percentOf[name],
            persist,
          })
        );
      }
      // need to know which calculator the user is entering for mobile slider
      dispatch(setActiveCalculator({ name: "capRate", persist }));
    },
    [
      itemizationValues,
      percentOfAmountObj,
      percentOf,
      purchase_price,
      dispatch,
      persist,
    ]
  );

  const handleAdd = useCallback(() => {
    dispatch(
      updateItemization({
        name: eCalcForms.CLOSING_COSTS,
        value: {
          ...itemizationValues,
          [`new_cost_${totalAddedFields + 1}`]: { dollar: 0 },
        },
        persist,
      })
    );
  }, [dispatch, persist, itemizationValues, totalAddedFields]);

  const handleReset = useCallback(() => {
    dispatch(
      updateItemization({
        name: eCalcForms.CLOSING_COSTS,
        value: initialValues,
        persist,
      })
    );
  }, [dispatch, initialValues, persist]);

  const handleUndoableDelete = useCallback((key) => {
    setItemsToHide((list) => [...list, key]);
  }, []);

  const undoDelete = useCallback((key) => {
    setItemsToHide((list) => list.filter((l) => key !== l));
  }, []);

  const handleDelete = useCallback(
    (key) => {
      const { [key]: itemToDelete, ...otherItems } = itemizationValues;
      dispatch(
        updateItemization({
          name: eCalcForms.CLOSING_COSTS,
          value: otherItems,
          persist,
        })
      );
      dispatch(
        deleteUserItemizationEntryMethod({
          itemizationName: entryMethodNames.CLOSING_COST_ITEMIZED,
          item: key,
          persist,
        })
      );
      undoDelete(key);
    },
    [dispatch, persist, itemizationValues, undoDelete]
  );

  const togglePercentOf = useCallback(
    ({ itemName, curPercentOf, dollarAmount }) => {
      const percentOfField =
        curPercentOf === LOAN_AMOUNT ? PURCHASE_PRICE : LOAN_AMOUNT;
      const percentOfAmount =
        curPercentOf === LOAN_AMOUNT ? purchase_price : loan_amount_dollar;
      const entryMethod = userItemizationEntryMethods[itemName];
      if (entryMethod) {
        const syncedFields = syncFields({
          nestedKey: entryMethod,
          value: itemizationValues[itemName][entryMethod],
          canBeSynced: true,
          percentOfAmount,
        });
        const newValues = {
          ...itemizationValues,
          [itemName]: { ...itemizationValues[itemName], ...syncedFields },
        };
        const {
          [entryMethods.DOLLAR_AMOUNT]: dollarAmt,
          [entryMethods.PERCENT_AMOUNT]: percentAmt,
        } = getTotalAmounts({
          itemizationValues: newValues,
          purchasePrice: purchase_price,
          canShowPercent: !!purchase_price,
        });
        dispatch(
          eCalcOnMultiChange({
            calculator: eCalcForms.EQUITY_NEEDED,
            valuesObj: {
              closing_costs_dollar: dollarAmt,
              closing_costs_percent: percentAmt,
            },
            persist,
          })
        );
        dispatch(
          updateItemization({
            name: eCalcForms.CLOSING_COSTS,
            value: newValues,
            persist,
          })
        );
      }
      dispatch(
        updateItemizationPercentOf({
          itemizationName: entryMethodNames.CLOSING_COST_ITEMIZED,
          item: itemName,
          value: percentOfField,
          persist,
        })
      );
    },
    [
      dispatch,
      persist,
      itemizationValues,
      loan_amount_dollar,
      purchase_price,
      userItemizationEntryMethods,
    ]
  );

  if (!itemizationValues) return null;
  return (
    <ItemizationLayout
      open={open}
      handleClose={handleClose}
      handleAdd={handleAdd}
      handleReset={handleReset}
      title="Closing Costs Itemized"
      totals={totals}
    >
      <List>
        {Object.keys(itemizationValues).map((itemName) => {
          const { dollar } = itemizationValues[itemName];
          const isDeleted = itemsToHide.indexOf(itemName) > -1;
          return (
            <Fragment key={itemName}>
              <ListItem
                classes={{ root: classes.listItem }}
                disableGutters
                innerRef={
                  itemName === `new_cost_${totalAddedFields}`
                    ? newValueRef
                    : undefined
                }
              >
                <EditableItemizationText
                  value={itemName}
                  name={itemName}
                  type="key"
                  handleBlur={handleKeysBlur}
                  handleUndoableDelete={handleUndoableDelete}
                  undoDelete={undoDelete}
                  handleDelete={handleDelete}
                  isDeleted={isDeleted}
                />
                <Grid
                  container
                  className={clsx({ [classes.hidden]: isDeleted })}
                >
                  <Grid item xs={4} />
                  <Grid item xs={3} classes={{ root: classes.gridItem }}>
                    <span className={classes.valuesKey}>Amount</span>
                  </Grid>
                  <Grid item xs={4} classes={{ root: classes.gridItem }}>
                    <EditableItemizationText
                      value={dollar}
                      name={itemName}
                      type="value"
                      nestedKey="dollar"
                      handleBlur={handleValuesBlur}
                      startAdornment="$"
                    />
                  </Grid>
                  {itemizationValues[itemName].hasOwnProperty("percent") && (
                    <Fragment>
                      <Grid item xs={2} />
                      <Grid item xs={5} classes={{ root: classes.gridItem }}>
                        <span
                          className={classes.valuesKey}
                          onClick={() =>
                            togglePercentOf({
                              itemName,
                              curPercentOf: percentOf[itemName],
                              dollarAmount: dollar,
                            })
                          }
                          style={{ cursor: "pointer" }}
                        >
                          {percentOf[itemName] === LOAN_AMOUNT
                            ? "% of Loan Amount"
                            : "% of Purchase Price"}
                        </span>
                      </Grid>
                      <Grid item xs={4} classes={{ root: classes.gridItem }}>
                        <EditableItemizationText
                          value={itemizationValues[itemName].percent}
                          name={itemName}
                          type="value"
                          nestedKey="percent"
                          handleBlur={handleValuesBlur}
                          endAdornment="%"
                          display="percent"
                        />
                      </Grid>
                    </Fragment>
                  )}
                </Grid>
              </ListItem>
              {!isDeleted && <Divider className={classes.divider} />}
            </Fragment>
          );
        })}
      </List>
    </ItemizationLayout>
  );
}

export function syncFields({ canBeSynced, nestedKey, value, percentOfAmount }) {
  const syncKey =
    nestedKey === entryMethods.DOLLAR_AMOUNT
      ? entryMethods.PERCENT_AMOUNT
      : entryMethods.DOLLAR_AMOUNT;
  // check if it could have a percent value
  if (!canBeSynced) {
    return {
      [entryMethods.DOLLAR_AMOUNT]: value,
    };
  }
  if (!percentOfAmount) {
    return {
      [nestedKey]: value,
      [syncKey]: 0,
    };
  }
  const syncValue =
    nestedKey === entryMethods.DOLLAR_AMOUNT
      ? formatPercent(parseToNumber(value) / parseToNumber(percentOfAmount))
      : value
      ? noDecimal(numberToPercent(value) * parseToNumber(percentOfAmount))
      : 0;
  return {
    [nestedKey]: value,
    [syncKey]: syncValue,
  };
}

export function getTotalAmounts({
  itemizationValues,
  purchasePrice,
  canShowPercent,
}) {
  const dollarAmounts = _sum(
    _flatMap(itemizationValues, (item) =>
      parseToNumber(item[entryMethods.DOLLAR_AMOUNT] || 0)
    )
  );
  const rv = {
    [entryMethods.DOLLAR_AMOUNT]: dollarAmounts,
  };
  if (canShowPercent) {
    rv[entryMethods.PERCENT_AMOUNT] = formatPercent(
      parseToNumber(dollarAmounts) / parseToNumber(purchasePrice)
    );
  }
  return rv;
}
