import { parseToNumber, formatPercent } from "../helpers";
import { monthlyAnnualPaymentsAmortizationPeriod } from "./MortgageDscr.logic";

export function calculateFreeCashFlow({
  first_year_noi,
  loan_amount_dollar,
  interest_rate,
  amortization_years,
  io_years,
}) {
  var results = first_year_noi;
  if (
    first_year_noi &&
    loan_amount_dollar &&
    interest_rate &&
    amortization_years
  ) {
    const { annual: annualPayments } = monthlyAnnualPaymentsAmortizationPeriod({
      loan_amount_dollar,
      interest_rate,
      amortization_years,
    });
    let payments = annualPayments;
    if (io_years) {
      payments =
        parseToNumber(loan_amount_dollar) *
        (parseToNumber(interest_rate) / 100);
    }
    results =
      annualPayments !== "Invalid Entry"
        ? parseToNumber(first_year_noi) - payments
        : annualPayments;
  }
  return {
    label: "First Year Net Cash Flow",
    results,
    displayFormat: "dollar",
  };
}

export function calcFirstYearCashOnCash({
  first_year_noi,
  loan_amount_dollar,
  interest_rate,
  amortization_years,
  equity_needed,
  io_years,
}) {
  var results = 0;
  const { results: freeCashFlow } = calculateFreeCashFlow({
    first_year_noi,
    loan_amount_dollar,
    interest_rate,
    amortization_years,
    io_years,
  });

  results =
    freeCashFlow !== "Invalid Entry"
      ? formatPercent(
          parseToNumber(freeCashFlow) / parseToNumber(equity_needed)
        )
      : freeCashFlow;
  return {
    label: "First Year Cash on Cash Return",
    results,
    displayFormat: "percent",
  };
}
