import React, { useEffect, useState } from "react";
import clsx from "clsx";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import { TextField, InputAdornment } from "@material-ui/core";
import { eCalcForms } from "../../../lib";
import {
  setIsDirty,
  eCalcOnChange,
  setActiveCalculator,
  eCalcOnMultiChange,
  removeResults,
} from "../../../actions/eCalcForms";
import { useMobile } from "../../../themes";
import { MissingInputsMsg } from "../components";
import { calculateStats } from "./EquityWaterfall.logic";
import { useStyles } from "./EquityWaterfall.styles";

export function EquityWaterfall({ storeSliceName, persist }) {
  const classes = useStyles();
  const isMobile = useMobile();
  const dispatch = useDispatch();

  const dirty = useSelector((state) => state[storeSliceName].dirty);
  const equityWaterfallState = useSelector((state) => {
    const {
      equityWaterfall,
      cashOnCash: { equity_needed },
      irrSchedule: { net_cash_flow = [], capitalEventNetCashFlow },
    } = state[storeSliceName];
    return {
      ...equityWaterfall,
      equityNeededDisplay: equity_needed,
      netCashFlow: net_cash_flow,
      capitalEventNetCashFlow,
    };
  }, shallowEqual);

  let {
    gpPercent,
    lpPercent,
    gpDollars,
    lpDollars,
    equityNeededDisplay,
    netCashFlow,
    hurdles,
  } = equityWaterfallState;
  const equityNeeded =
    equityNeededDisplay && equityNeededDisplay !== "0"
      ? formatDollarDisplay(equityNeededDisplay)
      : 0;

  const [showInputsMissingMsg, setShowInputsMissingMsg] = useState(false);
  const [endPercentPlaceholder, setEndPercentPlaceholder] = useState(
    "Infinite"
  );

  useEffect(() => {
    if (equityNeeded && (gpPercent || lpPercent || gpDollars || lpDollars)) {
      lpPercent
        ? calculateFromLpPercent(lpPercent)
        : gpPercent
        ? calculateFromGpPercent(gpPercent)
        : lpDollars
        ? calculateFromLpDollars(lpDollars)
        : calculateFromGpDollars(gpDollars);
    }
    if (!equityNeeded) {
      dispatchMultiChange({ lpDollars: "", gpDollars: "" }, persist);
      dispatch(removeResults({ name: "lp_stats", persist }));
      dispatch(removeResults({ name: "gp_stats", persist }));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [equityNeeded]);

  useEffect(() => {
    if (
      equityNeeded &&
      netCashFlow.length &&
      gpPercent &&
      lpPercent &&
      gpDollars &&
      lpDollars &&
      hurdles.length
    ) {
      calculateStats(equityWaterfallState, dispatch, persist);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    equityNeeded,
    gpPercent,
    lpPercent,
    gpDollars,
    lpDollars,
    hurdles,
    netCashFlow,
    persist,
  ]);

  useEffect(() => {
    if (!equityNeeded || !netCashFlow) {
      setShowInputsMissingMsg(true);
    } else {
      setShowInputsMissingMsg(false);
    }
  }, [equityNeeded, netCashFlow]);

  function calculateFromGpPercent(gpPercent) {
    const gpDollars = getDollars(gpPercent);
    dispatchMultiChange(
      {
        gpPercent,
        gpDollars,
        lpDollars: formatDollarDisplay(subtract(equityNeeded, gpDollars)),
        lpPercent: formatValue(subtract(100, gpPercent)),
      },
      persist
    );
  }

  function calculateFromLpPercent(lpPercent) {
    const lpDollars = getDollars(lpPercent);
    dispatchMultiChange(
      {
        lpPercent,
        lpDollars,
        gpDollars: formatDollarDisplay(subtract(equityNeeded, lpDollars)),
        gpPercent: formatValue(subtract(100, lpPercent)),
      },
      persist
    );
  }

  function calculateFromGpDollars(gpDollars) {
    const gpPercent = getPercent(gpDollars);
    dispatchMultiChange(
      {
        gpDollars,
        lpDollars: formatDollarDisplay(subtract(equityNeeded, gpDollars)),
        gpPercent,
        lpPercent: formatValue(subtract(100, gpPercent)),
      },
      persist
    );
  }

  function calculateFromLpDollars(lpDollars) {
    const lpPercent = getPercent(lpDollars);
    dispatchMultiChange(
      {
        lpDollars,
        gpDollars: formatDollarDisplay(subtract(equityNeeded, lpDollars)),
        lpPercent,
        gpPercent: formatValue(subtract(100, lpPercent)),
      },
      persist
    );
  }

  function dispatchMultiChange(valuesObj, persist) {
    dispatch(
      eCalcOnMultiChange({
        calculator: eCalcForms.EQUITY_WATERFALL,
        valuesObj,
        persist,
      })
    );
  }

  function getPercent(dollars) {
    return ((+dollars * 100) / +equityNeeded).toFixed(2);
  }

  function getDollars(percent) {
    return formatDollarDisplay((+percent / 100) * +equityNeeded);
  }

  function subtract(bigNum, smallNum) {
    const bigNumInt = +bigNum * 100;
    const smallNumInt = +smallNum * 100;
    return (bigNumInt - smallNumInt) / 100;
  }

  function handleChange(e) {
    const { name, value: rawValue } = e.target;
    const value = formatValue(rawValue);
    switch (name) {
      case "lpPercent":
        calculateFromLpPercent(value);
        break;
      case "gpPercent":
        calculateFromGpPercent(value);
        break;
      case "lpDollars":
        calculateFromLpDollars(value);
        break;
      case "gpDollars":
        calculateFromGpDollars(value);
        break;
      default:
        break;
    }
    dispatch(setActiveCalculator({ name: "equityWaterfall", persist }));
    if (!dirty) {
      dispatch(setIsDirty(persist));
    }
  }

  function handleHurdleChange(e, index) {
    const { name, value: rawValue } = e.target;
    const value = formatValue(rawValue);
    const emptyHurdle = {
      startPercent: value,
      endPercent: "",
      promotePercent: "",
    };
    let hurdlesC = [...hurdles];

    if (name === "endPercent") {
      // if changing one of middle percents, remove later hurdles
      // TODO: find out if this is the desired behaviour
      if (hurdles.length - 1 !== index) {
        hurdlesC = hurdles.slice(0, index + 1);
        if (value) {
          setEndPercentPlaceholder("Infinite");
          hurdlesC.push(emptyHurdle);
        } else {
          setEndPercentPlaceholder("");
        }
      } else {
        setEndPercentPlaceholder("Infinite");
        hurdlesC.push(emptyHurdle);
      }
    }

    hurdlesC[index][name] = value;
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.EQUITY_WATERFALL,
        name: "hurdles",
        value: hurdlesC,
      })
    );
    dispatch(setActiveCalculator({ name: "equityWaterfall", persist }));
  }

  return (
    <div className={classes.calcContainer}>
      {showInputsMissingMsg && (
        <MissingInputsMsg calculator="Equity Waterfall" persist={persist} />
      )}
      <TextField
        className={clsx(classes.margin, classes.fullWidthInput)}
        label="Total Equity Needed:"
        variant="outlined"
        value={equityNeededDisplay}
        onChange={() => {}}
        fullWidth
        disabled
        InputProps={{
          startAdornment: (
            <InputAdornment
              position="end"
              classes={{ root: classes.inputAdornment }}
            >
              $
            </InputAdornment>
          ),
          classes: { input: classes.inputWithPadding },
        }}
        InputLabelProps={{
          classes: {
            root: clsx(classes.inputLabel),
            shrink: "shrink",
          },
          shrink: true,
        }}
      />
      <div className={classes.lineInputsContainer}>
        <TextField
          inputProps={{ inputMode: "decimal" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Sponsor (GP) Contribution %:"
          variant="outlined"
          value={gpPercent}
          onChange={handleChange}
          name="gpPercent"
          error={gpPercent > 100}
          helperText={
            gpPercent > 100 ? "Value may not be greater then 100" : ""
          }
          InputProps={{
            classes: {
              root: clsx({ [classes.wrappedLabelMargin]: isMobile }),
              input: classes.inputWithPadding,
            },
            endAdornment: (
              <InputAdornment
                position="end"
                classes={{ root: classes.inputAdornment }}
              >
                %
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: {
              root: clsx(classes.inputLabel, {
                [classes.longLabel]: !isMobile,
              }),
              shrink: "shrink",
            },
            shrink: true,
          }}
        />
        <span
          className={clsx(
            classes.inputsDivider,
            classes.mobileMultilineLabelInputsDivider
          )}
        />
        <TextField
          inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Sponsor (GP) Contribution $:"
          variant="outlined"
          value={numberWithCommas(gpDollars)}
          onChange={handleChange}
          name="gpDollars"
          InputProps={{
            classes: {
              root: clsx({ [classes.wrappedLabelMargin]: isMobile }),
              input: classes.input,
            },
            startAdornment: (
              <InputAdornment
                position="start"
                classes={{ root: classes.inputAdornment }}
              >
                $
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: {
              root: clsx(classes.inputLabel, {
                [classes.longLabel]: !isMobile,
              }),
              shrink: "shrink",
            },
          }}
        />
      </div>
      <div className={classes.lineInputsContainer}>
        <TextField
          inputProps={{ inputMode: "decimal" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Investor (LP) Contribution %:"
          variant="outlined"
          value={lpPercent}
          onChange={handleChange}
          name="lpPercent"
          error={lpPercent > 100}
          helperText={
            lpPercent > 100 ? "Value may not be greater then 100" : ""
          }
          InputProps={{
            classes: {
              root: clsx({ [classes.wrappedLabelMargin]: isMobile }),
              input: classes.inputWithPadding,
            },
            endAdornment: (
              <InputAdornment
                position="end"
                classes={{ root: classes.inputAdornment }}
              >
                %
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: {
              root: clsx(classes.inputLabel, {
                [classes.longLabel]: !isMobile,
              }),
              shrink: "shrink",
            },
            shrink: true,
          }}
        />
        <span
          className={clsx(
            classes.inputsDivider,
            classes.mobileMultilineLabelInputsDivider
          )}
        />
        <TextField
          inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Investor (LP) Contribution $:"
          variant="outlined"
          value={numberWithCommas(lpDollars)}
          onChange={handleChange}
          name="lpDollars"
          InputProps={{
            classes: {
              root: clsx({ [classes.wrappedLabelMargin]: isMobile }),
              input: classes.input,
            },
            startAdornment: (
              <InputAdornment
                position="start"
                classes={{ root: classes.inputAdornment }}
              >
                $
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: {
              root: clsx(classes.inputLabel, {
                [classes.longLabel]: !isMobile,
              }),
              shrink: "shrink",
            },
          }}
        />
      </div>

      {hurdles.map((h, index) => {
        const { startPercent, endPercent, promotePercent } = h;
        return (
          <div key={index}>
            <div className={classes.hurdleTitle}>{`Hurdle ${index + 1}`}</div>
            <div
              className={classes.lineInputsContainer}
              style={{ alignItems: "flex-start" }}
            >
              <TextField
                className={clsx(classes.margin, classes.smallInput)}
                label="Return From:"
                variant="outlined"
                value={startPercent}
                onChange={() => {}}
                disabled
                InputProps={{
                  endAdornment: (
                    <InputAdornment
                      position="end"
                      classes={{ root: classes.inputAdornment }}
                    >
                      %
                    </InputAdornment>
                  ),
                  classes: { input: classes.inputWithPadding },
                }}
                InputLabelProps={{
                  classes: {
                    root: clsx(classes.inputLabel),
                    shrink: "shrink",
                  },
                  shrink: true,
                }}
              />
              <span className={classes.inputsDivider} style={{ height: 0 }} />
              <TextField
                inputProps={{ inputMode: "decimal" }}
                className={clsx(classes.margin, classes.smallInput)}
                label="Return Up To:"
                variant="outlined"
                value={endPercent}
                onChange={(e) => handleHurdleChange(e, index)}
                name="endPercent"
                placeholder={endPercentPlaceholder}
                onBlur={() =>
                  hurdles.length - 1 === index
                    ? setEndPercentPlaceholder("Infinite")
                    : null
                }
                onFocus={() =>
                  hurdles.length - 1 === index
                    ? setEndPercentPlaceholder("")
                    : null
                }
                error={Boolean(
                  endPercent &&
                    parseFloat(endPercent) < parseFloat(startPercent)
                )}
                helperText={
                  endPercent &&
                  parseFloat(endPercent) < parseFloat(startPercent)
                    ? "Value must be greater than Return From"
                    : ""
                }
                InputProps={{
                  endAdornment: (
                    <InputAdornment
                      position="end"
                      classes={{ root: classes.inputAdornment }}
                    >
                      %
                    </InputAdornment>
                  ),
                  classes: { input: classes.inputWithPadding },
                }}
                InputLabelProps={{
                  classes: {
                    root: clsx(classes.inputLabel),
                    shrink: "shrink",
                  },
                  shrink: true,
                }}
              />
            </div>
            <TextField
              inputProps={{ inputMode: "decimal" }}
              className={clsx(classes.margin, classes.fullWidthInput)}
              label="Promote to Sponsor (GP):"
              variant="outlined"
              value={promotePercent}
              onChange={(e) => handleHurdleChange(e, index)}
              name="promotePercent"
              fullWidth
              InputProps={{
                endAdornment: (
                  <InputAdornment
                    position="end"
                    classes={{ root: classes.inputAdornment }}
                  >
                    %
                  </InputAdornment>
                ),
                classes: { input: classes.inputWithPadding },
              }}
              InputLabelProps={{
                classes: {
                  root: clsx(
                    classes.inputLabel,
                    classes.fullWidthLabel,
                    classes.longLabel
                  ),
                  shrink: "fullInputShrink",
                },
                shrink: true,
              }}
            />
          </div>
        );
      })}
    </div>
  );
}

function formatValue(value) {
  let formatted = `${value}`.replace(/[^0-9.]/g, "");
  const decIndex = formatted.indexOf(".");
  if (decIndex > -1) {
    formatted = formatted.replace(/(\.[^.]*)\./g, "$1").slice(0, decIndex + 3);
  }
  return formatted;
}

function formatDollarDisplay(value) {
  let formatted = `${value}`.replace(/[^0-9.]/g, "");
  const decIndex = formatted.indexOf(".");
  if (decIndex > -1) {
    formatted = `${formatted}00`;
    formatted = formatted.slice(0, decIndex + 3);
  }
  return formatted;
}

function numberWithCommas(num) {
  return `${num}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}
