import React, { useEffect, useState, Fragment } from "react";
import clsx from "clsx";
import { useDispatch } from "react-redux";
import {
  // makeStyles,
  useMediaQuery,
  Paper,
} from "@material-ui/core";
import { Calculator } from "./Calculator";
import { SideResults } from "./results";
import {
  EcalcSideRates,
  EcalcMobileRatesSlider,
  EcalcDesktopRatesSlider,
  CalculatorToolbar,
} from "./components";
import { updateNavTitle } from "../../actions/navigationTitleActions";
import { ECALC_SHOW_SIDE_RESULTS, ECALC_SHOW_SIDE_RATES } from "../../lib";
import { useMobile } from "../../themes";
import { useStyles } from "./CalculatorContainer.styles";
import { parse } from "query-string";
import { getPublicEcalcById } from "../../actions/eCalcForms";

export default function CalculatorContainer({
  isDealDetails,
  hasToolbar,
  handleSave,
}) {
  const showSideRates = useMediaQuery(ECALC_SHOW_SIDE_RATES);
  const showSideResults = useMediaQuery(ECALC_SHOW_SIDE_RESULTS);
  const classes = useStyles({ showSideRates, isDealDetails });
  const isMobile = useMobile();
  const dispatch = useDispatch();
  const [storeSliceName, setStoreSliceName] = useState();
  const { hash_id } = parse(window.location.search);
  const isPublicEcalc = !!hash_id;

  useEffect(() => {
    setStoreSliceName(
      isDealDetails || isPublicEcalc ? "eCalcFormsNoPersist" : "eCalcForm"
    );
  }, [isDealDetails, isPublicEcalc]);

  useEffect(() => {
    if (isPublicEcalc) {
      dispatch(getPublicEcalcById({ hash_id }));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isPublicEcalc]);

  useEffect(() => {
    if (!isDealDetails) {
      dispatch(updateNavTitle("eCALC"));
    }
  }, [dispatch, isDealDetails]);
  // const [page, setPage] = useState(0);

  // const handlePageTabs = (event, newValue) => {
  //   setPage(newValue);
  // };

  if (!storeSliceName) return null;

  return (
    <Fragment>
      <CalculatorToolbar
        storeSliceName={storeSliceName}
        isDealDetails={isDealDetails}
        persist={!isDealDetails && !isPublicEcalc}
        isMobile={isMobile}
        handleSave={handleSave}
      />
      <Paper
        className={clsx(classes.cssReset, classes.container)}
        style={{ paddingTop: !isMobile ? 20 : undefined }} // Remove after tabs are implemented
      >
        {/* {!isMobile && (
        <Tabs
          value={page}
          indicatorColor="primary"
          onChange={handlePageTabs}
          classes={{ root: classes.tabsContainer }}
        >
          <Tab
            label="The E Calculator"
            classes={{
              root: classes.resultsTab,
              selected: classes.resultsTabActive
            }}
          />
          <Tab
            label="My Saved Underwritings"
            classes={{
              root: classes.resultsTab,
              selected: classes.resultsTabActive
            }}
            disabled
          />
        </Tabs>
      )} */}
        <div className={classes.layout}>
          {showSideRates && !isDealDetails && <EcalcSideRates />}
          <Calculator
            persist={!isDealDetails && !isPublicEcalc}
            isDealDetails={isDealDetails}
            storeSliceName={storeSliceName}
            hasToolbar={hasToolbar}
          />
          {!isMobile && showSideResults && (
            <SideResults
              storeSliceName={storeSliceName}
              isDealDetails={isDealDetails}
              persist={!isDealDetails && !isPublicEcalc}
            />
          )}
          {!showSideRates && !isDealDetails && !isMobile && (
            <EcalcDesktopRatesSlider />
          )}
          {!showSideRates && !isDealDetails && isMobile && (
            <EcalcMobileRatesSlider />
          )}
        </div>
      </Paper>
    </Fragment>
  );
}
