import React, { useState, useEffect, useCallback } from "react";
import clsx from "clsx";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import mixpanel from "mixpanel-browser";
import _flatMap from "lodash/flatMap";
import _sum from "lodash/sum";
import _debounce from "lodash/debounce";
import {
  // Divider,
  TextField,
  InputAdornment,
} from "@material-ui/core";
import { eCalcForms } from "../../../lib";
import {
  setIsDirty,
  eCalcOnChange,
  eCalcOnMultiChange,
  setActiveCalculator,
  addUserEntryMethod,
  addResults,
  removeResults,
  clearBlendedRateItemization,
  updateItemization,
} from "../../../actions/eCalcForms";
import {
  ClosingCostItemized,
  syncFields as syncClosingCostItemizationFields,
  getTotalAmounts as getClosingCostItemizationAmounts,
} from "../itemizations/ClosingCostItemized";
import { ListIcon } from "../../../themes";
import {
  BlendedRateItemization,
  MissingInputsMsg,
  DisabledInputsMsg,
} from "../components";
import {
  formatDollar,
  formatPercent,
  entryMethodNames,
  entryMethods,
  numberToPercent,
  parseToNumber,
  noDecimal,
} from "../helpers";
import { calcEquityNeeded, calcDebtYield } from "./EquityNeeded.logic";
import { calcTotalLoanAndBlendedRate } from "../components/BlendedRateItemization.logic";
import { useStyles } from "./EquityNeeded.styles";

export function EquityNeeded({ storeSliceName, persist }) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [showBlendedRateItemization, setShowBlendedRateItemization] = useState(
    false
  );
  const dirty = useSelector((state) => state[storeSliceName].dirty);
  const equityNeededValues = useSelector(
    (state) => state[storeSliceName][eCalcForms.EQUITY_NEEDED],
    shallowEqual
  );
  const purchase_price = useSelector(
    (state) => state[storeSliceName][eCalcForms.CAP_RATE].purchase_price
  );
  const first_year_noi = useSelector(
    (state) => state[storeSliceName][eCalcForms.CAP_RATE].first_year_noi
  );
  const userEntryMethods = useSelector(
    (state) => state[storeSliceName].userEntryMethods,
    shallowEqual
  );
  const closingCostItemizationValues = useSelector(
    (state) => state[storeSliceName].itemizations?.[eCalcForms.CLOSING_COSTS],
    shallowEqual
  );
  const closingCostItemizationPercentOf = useSelector(
    (state) =>
      state[storeSliceName].itemizationsPercentOf?.[
        entryMethodNames.CLOSING_COST_ITEMIZED
      ],
    shallowEqual
  );
  const closingCostUserItemizationEntryMethods = useSelector(
    (state) =>
      state[storeSliceName].userItemizationEntryMethods?.[
        eCalcForms.CLOSING_COSTS
      ],
    shallowEqual
  );
  const {
    closingCost,
    loanAmount,
    loanAmountOf: reduxLoanAmountOf,
  } = userEntryMethods;
  const {
    closing_costs_dollar,
    closing_costs_percent,
    loan_amount_dollar,
    loan_amount_percent,
    total_capitalization,
    loans,
  } = equityNeededValues || {};

  function handleInputChange(event) {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;
    const intValue = parseFloat(value.replace(/[^0-9.]/g, "")); //Strips of non-numeric characters besides decimal
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.EQUITY_NEEDED,
        name,
        value: formatInput(name, value),
        persist,
      })
    );
    // need to know which // need to know which calculator the user is entering for mobile slider the user is entering for mobile slider
    dispatch(setActiveCalculator({ name: "equityNeeded", persist }));
    typeof syncFields[name] === "function" &&
      syncFields[name]({
        value: intValue,
        dispatch,
        state: { ...equityNeededValues, purchase_price },
        meta: { loanAmountOf },
        persist,
      });
    if (!dirty) {
      dispatch(setIsDirty(persist));
    }
  }

  const [loanAmountOf, setLoanAmountOf] = useState(
    reduxLoanAmountOf || "purchasePrice"
  );
  const [showInputsMissingMsg, setShowInputsMissingMsg] = useState(false);

  const [loansList, setLoans] = useState(loans);
  const [totalLoan, setTotalLoan] = useState(0);
  const [totalLoanPercentage, setTotalLoanPercentage] = useState(0);
  const [blendedRate, setBlendedRate] = useState(0);
  const [showClosingCostItemization, setShowClosingCostItemization] = useState(
    false
  );
  const [isClosingCostItemized, setIsClosingCostItemized] = useState(false);
  const [showClosingCostDisabledMsg, setShowClosingCostDisabledMsg] = useState(
    false
  );

  useEffect(() => {
    if (!purchase_price || !loan_amount_dollar || !isClosingCostItemized) {
      if (closingCost === entryMethods.DOLLAR_AMOUNT) {
        syncFields.closing_costs_dollar({
          value: closing_costs_dollar,
          dispatch,
          state: { ...equityNeededValues, purchase_price },
          persist,
        });
      } else {
        syncFields.closing_costs_percent({
          value: closing_costs_percent,
          dispatch,
          state: { ...equityNeededValues, purchase_price },
          persist,
        });
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [purchase_price, isClosingCostItemized, persist]);

  useEffect(() => {
    if (purchase_price && loan_amount_dollar && isClosingCostItemized) {
      syncClosingCostItemization({
        itemizationValues: closingCostItemizationValues,
        percentOfAmountObj: {
          loan_amount_dollar,
          purchase_price,
        },
        percentOfFields: closingCostItemizationPercentOf,
        userItemizationEntryMethods: closingCostUserItemizationEntryMethods,
        purchasePrice: purchase_price,
        dispatch,
        persist,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loan_amount_dollar, purchase_price, isClosingCostItemized, persist]);

  useEffect(() => {
    calculateTotalCapitalization({
      state: { ...equityNeededValues, purchase_price },
      dispatch,
      persist,
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [closing_costs_dollar, purchase_price]);

  useEffect(() => {
    calculateLoanAmount({
      loanAmountOf,
      loanAmountMethod: loanAmount,
      state: { ...equityNeededValues, purchase_price },
      dispatch,
      persist,
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    dispatch,
    loanAmount,
    loanAmountOf,
    purchase_price,
    total_capitalization,
    loan_amount_dollar,
    persist,
  ]);

  useEffect(() => {
    if (!purchase_price) {
      setShowInputsMissingMsg(true);
    } else {
      setShowInputsMissingMsg(false);
    }
  }, [purchase_price]);

  useEffect(() => {
    if (loan_amount_dollar && total_capitalization) {
      const results = calcEquityNeeded({
        loan_amount_dollar,
        total_capitalization,
      });
      dispatch(
        addResults({
          name: "equity_needed",
          value: results,
          persist,
        })
      );
    } else {
      dispatch(removeResults({ name: "equity_needed", persist }));
    }
  }, [loan_amount_dollar, total_capitalization, dispatch, persist]);

  useEffect(() => {
    if (loan_amount_dollar && first_year_noi) {
      const results = calcDebtYield({
        loan_amount_dollar,
        first_year_noi,
      });

      dispatch(
        addResults({
          name: "debt_yield",
          value: results,
          persist,
        })
      );
    } else {
      dispatch(removeResults({ name: "debt_yield", persist }));
    }
  }, [loan_amount_dollar, first_year_noi, dispatch, persist]);

  const toggleLoanAmountOf = useCallback(
    (amountOf) => {
      setLoanAmountOf(amountOf);
      dispatch(
        addUserEntryMethod({
          name: entryMethodNames.LOAN_AMOUNT_OF,
          value:
            amountOf === "purchasePrice"
              ? entryMethods.PURCHASE_PRICE
              : entryMethods.TOTAL_CAPITALIZATION,
          persist,
        })
      );
    },
    [dispatch, persist]
  );

  useEffect(() => {
    const results = calcTotalLoanAndBlendedRate(loansList);
    setTotalLoan(formatDollar(results.totalLoan));
    setTotalLoanPercentage(
      loanAmountOf === "totalCapitalization"
        ? total_capitalization
          ? formatPercent(
              parseToNumber(results.totalLoan) /
                parseToNumber(total_capitalization)
            )
          : ""
        : purchase_price
        ? formatPercent(
            parseToNumber(results.totalLoan) / parseToNumber(purchase_price)
          )
        : ""
    );
    setBlendedRate(results.blendedRate);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loansList]);

  useEffect(() => {
    setLoans(loans);
  }, [loans]);

  useEffect(() => {
    setIsClosingCostItemized(
      checkIfItemizedDollarPercent(closingCostItemizationValues)
    );
    setShowClosingCostDisabledMsg(false);
  }, [closingCostItemizationValues]);

  const openBlendedRateItemization = useCallback(() => {
    mixpanel.track("ecalc_popup", { popup: "Blended Rates screen" });
    setShowBlendedRateItemization(true);
  }, []);

  function blendedRateItemizationClose() {
    setLoans(loans);
    setShowBlendedRateItemization(false);
  }

  const openClosingCostItemization = useCallback((e) => {
    e.stopPropagation();
    setShowClosingCostItemization(true);
  }, []);

  const closeClosingCostItemization = useCallback(() => {
    setShowClosingCostItemization(false);
  }, []);

  const isItemized = Array.isArray(loans) && loans.some((loan) => loan.amount);
  return (
    <div className={classes.calcContainer}>
      {showInputsMissingMsg && (
        <MissingInputsMsg calculator="Equity Needed" persist={persist} />
      )}
      {showClosingCostDisabledMsg && (
        <DisabledInputsMsg field={"Closing Cost"} />
      )}
      <div className={classes.lineInputsContainer}>
        <TextField
          inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Closing Costs $:"
          variant="outlined"
          value={
            isClosingCostItemized
              ? formatDollar(parseToNumber(closing_costs_dollar))
              : closing_costs_dollar
          }
          disabled={isClosingCostItemized}
          onClick={
            isClosingCostItemized
              ? () => setShowClosingCostDisabledMsg(true)
              : undefined
          }
          onChange={handleInputChange}
          name="closing_costs_dollar"
          InputProps={{
            classes: { input: classes.input },
            startAdornment: (
              <InputAdornment
                position="start"
                classes={{ root: classes.inputAdornment }}
              >
                $
              </InputAdornment>
            ),
            endAdornment: (
              <InputAdornment position="end">
                <ListIcon
                  className={classes.listIcon}
                  onClick={openClosingCostItemization}
                />
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
          }}
        />
        <span className={classes.inputsDivider} />
        <TextField
          inputProps={{ inputMode: "decimal" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Closing cost %:"
          variant="outlined"
          value={closing_costs_percent}
          disabled={isClosingCostItemized}
          onClick={
            isClosingCostItemized
              ? () => setShowClosingCostDisabledMsg(true)
              : undefined
          }
          onChange={handleInputChange}
          name="closing_costs_percent"
          InputProps={{
            endAdornment: (
              <InputAdornment
                position="end"
                classes={{ root: classes.inputAdornment }}
              >
                %
              </InputAdornment>
            ),
            classes: { input: classes.inputWithPadding },
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
            shrink: true,
          }}
        />
      </div>

      <TextField
        className={clsx(classes.margin, classes.fullWidthInput)}
        label="Total Capitalization:"
        variant="outlined"
        value={formatDollar(total_capitalization)}
        onChange={() => {}}
        name="total_capitalization"
        fullWidth
        disabled
        InputProps={{
          classes: { input: classes.input },
          startAdornment: (
            <InputAdornment
              position="start"
              classes={{ root: classes.inputAdornment }}
            >
              $
            </InputAdornment>
          ),
        }}
        InputLabelProps={{
          classes: {
            root: clsx(classes.inputLabel, classes.fullWidthLabel),
            shrink: "fullInputShrink",
          },
          shrink: true,
        }}
      />
      <div className={classes.lineInputsContainer}>
        <TextField
          inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
          className={clsx(classes.margin, classes.smallInput)}
          label={isItemized ? "Total Loan Amount" : "Loan Amount as $:"}
          disabled={!!isItemized}
          variant="outlined"
          value={isItemized ? totalLoan : loan_amount_dollar}
          onChange={handleInputChange}
          name="loan_amount_dollar"
          InputProps={{
            classes: { input: classes.input },
            startAdornment: (
              <InputAdornment
                position="start"
                classes={{ root: classes.inputAdornment }}
              >
                $
              </InputAdornment>
            ),
            endAdornment: (
              <InputAdornment position="end">
                <ListIcon
                  className={classes.listIcon}
                  onClick={openBlendedRateItemization}
                />
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
          }}
        />
        <span className={classes.inputsDivider} />
        <TextField
          inputProps={{ inputMode: "decimal" }}
          className={clsx(classes.margin, classes.smallInput)}
          label={isItemized ? "Total Loan Amount as %" : "Loan Amount as %:"}
          variant="outlined"
          disabled={!!isItemized}
          value={isItemized ? totalLoanPercentage : loan_amount_percent}
          onChange={handleInputChange}
          name="loan_amount_percent"
          InputProps={{
            endAdornment: (
              <InputAdornment
                position="end"
                classes={{ root: classes.inputAdornment }}
              >
                %
              </InputAdornment>
            ),
            classes: { input: classes.inputWithPadding },
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
            shrink: true,
          }}
        />
      </div>
      <div className={classes.toggleContainer}>
        <span className={classes.toggle}>% of</span>
        <span
          onClick={() =>
            isItemized ? {} : toggleLoanAmountOf("purchasePrice")
          }
          className={clsx(classes.toggle, classes.pointer, {
            [classes.toggleActive]: loanAmountOf === "purchasePrice",
          })}
        >
          Purchase Price
        </span>
        <span className={classes.toggle}>|</span>
        <span
          onClick={() =>
            isItemized ? {} : toggleLoanAmountOf("totalCapitalization")
          }
          className={clsx(classes.toggle, classes.pointer, {
            [classes.toggleActive]: loanAmountOf === "totalCapitalization",
          })}
        >
          Total Capitalization
        </span>
      </div>

      {showBlendedRateItemization && (
        <BlendedRateItemization
          loans={loansList}
          blendedRate={blendedRate}
          totalLoan={totalLoan}
          totalLoanPercentage={totalLoanPercentage}
          setLoans={(loans) => setLoans(loans)}
          onClear={() => dispatch(clearBlendedRateItemization(persist))}
          onClose={() => blendedRateItemizationClose()}
          persist={persist}
        />
      )}
      {showClosingCostItemization && (
        <ClosingCostItemized
          open={showClosingCostItemization}
          handleClose={closeClosingCostItemization}
          storeSliceName={storeSliceName}
          persist={persist}
        />
      )}
    </div>
  );
}

const syncFields = {
  closing_costs_dollar({ value, dispatch, state, persist }) {
    const { purchase_price } = state;
    if (!purchase_price) return;
    console.log("persist1 ", persist);
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.EQUITY_NEEDED,
        name: "closing_costs_percent",
        value: formatPercent(
          parseToNumber(value) / parseToNumber(purchase_price)
        ),
        persist,
      })
    );
    dispatch(
      addUserEntryMethod({
        name: entryMethodNames.CLOSING_COST,
        value: entryMethods.DOLLAR_AMOUNT,
        persist,
      })
    );
  },
  closing_costs_percent({ value, dispatch, state, persist }) {
    const { purchase_price } = state;
    if (!purchase_price) return;
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.EQUITY_NEEDED,
        name: "closing_costs_dollar",
        value: value
          ? noDecimal(numberToPercent(value) * parseToNumber(purchase_price))
          : "",
        persist,
      })
    );
    dispatch(
      addUserEntryMethod({
        name: entryMethodNames.CLOSING_COST,
        value: entryMethods.PERCENT_AMOUNT,
        persist,
      })
    );
  },

  loan_amount_dollar({ value, dispatch, state, meta, persist }) {
    const { loanAmountOf } = meta;
    const { purchase_price, total_capitalization } = state;
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.EQUITY_NEEDED,
        name: "loan_amount_percent",
        value:
          loanAmountOf === "totalCapitalization"
            ? total_capitalization
              ? formatPercent(
                  parseToNumber(value) / parseToNumber(total_capitalization)
                )
              : ""
            : purchase_price
            ? formatPercent(
                parseToNumber(value) / parseToNumber(purchase_price)
              )
            : "",
        persist,
      })
    );
    dispatch(
      addUserEntryMethod({
        name: entryMethodNames.LOAN_AMOUNT,
        value: entryMethods.DOLLAR_AMOUNT,
        persist,
      })
    );
  },
  loan_amount_percent({ value, dispatch, state, meta, persist }) {
    const { loanAmountOf } = meta;
    const { purchase_price, total_capitalization } = state;
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.EQUITY_NEEDED,
        name: "loan_amount_dollar",
        value: value
          ? noDecimal(
              loanAmountOf === "totalCapitalization"
                ? total_capitalization
                  ? numberToPercent(value) * parseToNumber(total_capitalization)
                  : ""
                : purchase_price
                ? numberToPercent(value) * parseToNumber(purchase_price)
                : ""
            )
          : "",
        persist,
      })
    );
    dispatch(
      addUserEntryMethod({
        name: entryMethodNames.LOAN_AMOUNT,
        value: entryMethods.PERCENT_AMOUNT,
        persist,
      })
    );
  },
};

const syncClosingCostItemization = _debounce(
  ({
    itemizationValues = {},
    userItemizationEntryMethods = {},
    percentOfAmountObj,
    percentOfFields,
    purchasePrice,
    dispatch,
    persist,
  }) => {
    const syncedValues = Object.keys(itemizationValues).reduce((acc, cur) => {
      const entryMethod =
        userItemizationEntryMethods[cur] || entryMethods.DOLLAR_AMOUNT;
      const syncedFields = syncClosingCostItemizationFields({
        nestedKey: entryMethod,
        value: itemizationValues[cur][entryMethod],
        percentOfAmount: percentOfAmountObj[percentOfFields[cur]],
        canBeSynced: itemizationValues[cur].hasOwnProperty("percent"),
      });
      acc[cur] = { ...syncedFields };
      return acc;
    }, {});

    const {
      [entryMethods.DOLLAR_AMOUNT]: dollarAmt,
      [entryMethods.PERCENT_AMOUNT]: percentAmt,
    } = getClosingCostItemizationAmounts({
      itemizationValues: syncedValues,
      purchasePrice,
      canShowPercent: true,
    });
    dispatch(
      eCalcOnMultiChange({
        calculator: eCalcForms.EQUITY_NEEDED,
        valuesObj: {
          closing_costs_dollar: dollarAmt,
          closing_costs_percent: percentAmt,
        },
        persist,
      })
    );
    dispatch(
      updateItemization({
        name: eCalcForms.CLOSING_COSTS,
        value: syncedValues,
        persist,
      })
    );
  },
  300
);

function calculateTotalCapitalization({ state, dispatch, persist }) {
  const { purchase_price, closing_costs_dollar } = state;
  let results = "";
  if (purchase_price && closing_costs_dollar) {
    results =
      parseToNumber(closing_costs_dollar) + parseToNumber(purchase_price);
  }
  dispatch(
    eCalcOnChange({
      calculator: eCalcForms.EQUITY_NEEDED,
      name: "total_capitalization",
      value: results,
      persist,
    })
  );
}

function calculateLoanAmount({
  loanAmountOf,
  loanAmountMethod,
  state,
  dispatch,
  persist,
}) {
  const { loan_amount_dollar, loan_amount_percent } = state;
  if (loanAmountMethod === entryMethods.PERCENT_AMOUNT) {
    syncFields.loan_amount_percent({
      value: loan_amount_percent,
      dispatch,
      state,
      meta: { loanAmountOf },
      persist,
    });
  } else {
    syncFields.loan_amount_dollar({
      value: loan_amount_dollar,
      dispatch,
      state,
      meta: { loanAmountOf },
      persist,
    });
  }
}

function formatInput(name, value) {
  switch (name) {
    case "closing_costs_dollar":
    case "loan_amount_dollar":
      return noDecimal(parseToNumber(value));
    default:
      return value;
  }
}

function checkIfItemizedDollarPercent(itemizationValues) {
  const sum = _sum(
    _flatMap(itemizationValues, (item) =>
      parseToNumber(item[entryMethods.DOLLAR_AMOUNT] || 0)
    )
  );
  return sum > 0;
}
