import React, { useState, Fragment, useCallback } from "react";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import clsx from "clsx";
import {
  useMediaQuery,
  List,
  ListItem,
  Divider,
  Tab,
  Tabs,
} from "@material-ui/core";
import { SettingsOutlinedIcon /*, MoreHoriz, CloseIcon */ } from "../../themes";
import {
  useEcalcResults,
  MaxBankWillLend,
  IRRSchedule,
  AmortizationScheduleModal,
} from "./components";
import { formatDollar } from "./helpers";
import { eCalcForms, ECALC_SHOW_SIDE_RATES } from "../../lib";
import { irrScheduleChange } from "../../actions/eCalcForms";
import { calculateIRRSchedule } from "./components/IRRSchedule.logic";
import { calcBalanceAtMaturity } from "./calculators/MortgageDscr.logic";
import { DebtYieldModal } from "./components/DebtYieldModal";
import { useStyles } from "./results.styles";
import mixpanel from "mixpanel-browser";

export function SideResults({
  isMobileResults,
  setShowMobileResultsPage,
  storeSliceName,
  isDealDetails,
  persist,
}) {
  const showSideRates = useMediaQuery(ECALC_SHOW_SIDE_RATES);
  const classes = useStyles({ showSideRates, isMobileResults, isDealDetails });
  const [page, setPage] = useState(0);

  const handlePageTabs = useCallback((event, newValue) => {
    mixpanel.track(
      newValue === 0
        ? "Summary Tab on Results Clicked"
        : "Number Tab on Results Clicked"
    );
    setPage(newValue);
  }, []);

  return (
    <div
      className={clsx(classes.sideResults, {
        [classes.sideResultsDealDetails]: isDealDetails,
        [classes.mobileResultsPage]: isMobileResults,
      })}
    >
      {!isMobileResults ? (
        <div className={classes.resultsTitle}>Results</div>
      ) : (
        <div className={classes.threeDotsContainer}>
          <div className={classes.subTitle}>eCALC Results</div>
          {/* <MoreHoriz className={classes.threeDots} /> */}
        </div>
      )}
      <Tabs
        value={page}
        onChange={handlePageTabs}
        TabIndicatorProps={{
          style: { color: "#10B8C7", height: 1.10549 },
        }}
        classes={{ flexContainer: classes.tabFlex, root: classes.tabRoot }}
      >
        <Tab
          label="summary"
          classes={{
            root: classes.resultsTab,
            selected: classes.resultsTabActive,
          }}
        />
        <Tab
          label="Numbers"
          classes={{
            root: classes.resultsTab,
            selected: classes.resultsTabActive,
          }}
          style={{ marginLeft: 30 }}
        />
      </Tabs>
      {page === 0 && <SummaryView storeSliceName={storeSliceName} />}
      {page === 1 && (
        <NumbersView
          storeSliceName={storeSliceName}
          isDealDetails={isDealDetails}
          persist={persist}
        />
      )}
      {/* <BrokerComponent /> */}
    </div>
  );
}

export function NumbersView({ storeSliceName, isDealDetails, persist }) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { resultsCount, resultsValues } = useEcalcResults(storeSliceName);

  const hasResults = !!resultsCount > 0;

  const [showMaxBank, setShowMaxBank] = useState(false);
  const [showIRRScheduleModal, setShowIRRScheduleModal] = useState(false);
  const [showDebtYieldModal, setShowDebtYieldModal] = useState(false);
  const [
    showAmortizationScheduleModal,
    setShowAmortizationScheduleModal,
  ] = useState(false);

  const irrValues = useSelector(
    (state) => state[storeSliceName][eCalcForms.IRR],
    shallowEqual
  );
  const annual_expenses = useSelector(
    (state) => state[storeSliceName][eCalcForms.CAP_RATE].annual_expenses
  );
  const potential_annual_gross_income = useSelector(
    (state) =>
      state[storeSliceName][eCalcForms.CAP_RATE].potential_annual_gross_income
  );
  const vacancy_as_percent = useSelector(
    (state) => state[storeSliceName][eCalcForms.CAP_RATE].vacancy_as_percent
  );
  const equity_needed = useSelector(
    (state) => state[storeSliceName][eCalcForms.CASH_ON_CASH].equity_needed
  );
  const { loan_amount_dollar, total_capitalization } = useSelector(
    (state) => state[storeSliceName][eCalcForms.EQUITY_NEEDED]
  );
  const mortgageDscrValues = useSelector(
    (state) => state[storeSliceName][eCalcForms.MORTGAGE_AND_DSCR],
    shallowEqual
  );
  const mortgage_post_io = useSelector(
    (state) => state[storeSliceName].resultsValues.mortgage_post_io,
    shallowEqual
  );
  const mortgage_during_io = useSelector(
    (state) => state[storeSliceName].resultsValues.mortgage_during_io,
    shallowEqual
  );

  const { interest_rate, io_years, amortization_years } =
    mortgageDscrValues || {};

  const IrrScheduleValues = useSelector(
    (state) => state[storeSliceName].irrSchedule,
    shallowEqual
  );

  const {
    hasCapitalEvent,
    capitalEventType,
    capitalEventYear,
    capitalEventInterestRate,
    capitalEventAmortizationYears,
    capitalEventLoanAmount,
    capitalEventNetLoan,
  } = useSelector(
    (state) => state[storeSliceName].irrCapitalEvent,
    shallowEqual
  );

  const {
    holding_period_yrs,
    projected_gross_income_rate,
    stabilized_vacancy,
    projected_annual_expense,
    projected_cap_rate_at_sale,
    projected_closing_costs_at_sale,
    year_of_stabilization,
  } = irrValues || {};

  const { pgi, stabilized_vacancies, expenses } = IrrScheduleValues || {};

  function calculate() {
    const mortgageBalance = calcBalanceAtMaturity({
      loan_amount_dollar,
      interest_rate,
      amortization_years,
      holding_period_yrs,
      io_years,
    });

    const annual_mortgage_post_io =
      mortgage_post_io &&
      mortgage_post_io.results.find((m) => m.name === "Annual").value;
    const annual_mortgage_during_io =
      mortgage_during_io &&
      mortgage_during_io.results.find((m) => m.name === "Annual").value;
    const mortgage_balance_amount = mortgageBalance && mortgageBalance.results;

    const result = calculateIRRSchedule({
      holding_period_yrs,
      projected_gross_income_rate,
      stabilized_vacancy,
      projected_annual_expense,
      projected_cap_rate_at_sale,
      projected_closing_costs_at_sale,
      potential_annual_gross_income,
      vacancy_as_percent,
      annual_expenses,
      io_years,
      amortization_years,
      mortgage_balance_amount,
      annual_mortgage_post_io,
      annual_mortgage_during_io,
      equity_needed,
      pgi,
      stabilized_vacancies,
      expenses,
      year_of_stabilization,
      total_capitalization,
      hasCapitalEvent,
      capitalEventType,
      capitalEventYear,
      capitalEventInterestRate,
      capitalEventAmortizationYears,
      capitalEventLoanAmount,
      capitalEventNetLoan,
    });
    dispatch(irrScheduleChange(result, persist));
  }

  return (
    <div className={classes.listContainer}>
      {hasResults && !!Object.keys(resultsValues).length ? (
        <List>
          {Object.keys(resultsValues).map((key) => {
            const {
              label,
              results,
              displayFormat,
              showMaxLoan,
              showAmortizationSchedule,
              showIRRSchedule,
              showDebtYield,
            } = resultsValues[key] || {};
            if (!!results || results >= 0) {
              return Array.isArray(results) ? (
                <Fragment key={key}>
                  <ListItem
                    alignItems="center"
                    className={classes.resultsSectionContainer}
                  >
                    {!!showMaxLoan && (
                      <SettingsOutlinedIcon
                        className={classes.resultsSettingsIcon}
                        style={{ top: 20 }}
                        onClick={() => {
                          mixpanel.track("Max frm Dscr Modal Opened");
                          setShowMaxBank(true);
                        }}
                      />
                    )}

                    {!!showIRRSchedule && (
                      <SettingsOutlinedIcon
                        className={classes.resultsSettingsIcon}
                        style={{ top: 20 }}
                        onClick={() => setShowIRRScheduleModal(true)}
                      />
                    )}

                    {!!showDebtYield && (
                      <SettingsOutlinedIcon
                        className={classes.resultsSettingsIcon}
                        style={{ top: 20 }}
                        onClick={() => {
                          mixpanel.track("Debt Yield Modal Opened");
                          setShowDebtYieldModal(true);
                        }}
                      />
                    )}

                    <div
                      className={classes.resultsSectionListItem}
                      style={{ marginBottom: 7 }}
                    >
                      <span className={classes.resultsSectionTitle}>
                        {label}
                      </span>
                      {showAmortizationSchedule && (
                        <span
                          className={classes.resultsSectionLink}
                          onClick={() => {
                            mixpanel.track("amortization modal opened");
                            setShowAmortizationScheduleModal(true);
                          }}
                        >
                          View Schedule
                        </span>
                      )}
                    </div>
                    {results.map((result, index) => {
                      const {
                        name,
                        value,
                        displayFormat: resultsDisplayFormat,
                      } = result || {};
                      return (
                        <div
                          className={classes.resultsSectionListItem}
                          key={index}
                        >
                          <span className={classes.resultsKeys}>{name}</span>
                          <span className={classes.resultsValues}>
                            {formatDisplay(value, resultsDisplayFormat)}
                          </span>
                        </div>
                      );
                    })}
                  </ListItem>
                  <Divider className={classes.resultDivider} />
                </Fragment>
              ) : (
                <Fragment key={key}>
                  <ListItem
                    alignItems="center"
                    className={classes.resultsList}
                    style={{
                      paddingTop:
                        showMaxLoan || showIRRSchedule || showDebtYield
                          ? 15
                          : undefined,
                    }}
                  >
                    {!!showMaxLoan && (
                      <SettingsOutlinedIcon
                        className={classes.resultsSettingsIcon}
                        onClick={() => {
                          mixpanel.track("Max frm Dscr Modal Opened");
                          setShowMaxBank(true);
                        }}
                      />
                    )}

                    {!!showIRRSchedule && (
                      <SettingsOutlinedIcon
                        className={classes.resultsSettingsIcon}
                        onClick={() => setShowIRRScheduleModal(true)}
                      />
                    )}
                    {!!showDebtYield && (
                      <SettingsOutlinedIcon
                        className={classes.resultsSettingsIcon}
                        onClick={() => {
                          mixpanel.track("Debt Yield Modal Opened");
                          setShowDebtYieldModal(true);
                        }}
                      />
                    )}
                    <span className={classes.resultsKeys}>{label}:</span>
                    <span className={classes.resultsValues}>
                      {formatDisplay(results, displayFormat)}
                    </span>
                  </ListItem>
                  <Divider className={classes.resultDivider} />
                </Fragment>
              );
            } else return null;
          })}
        </List>
      ) : (
        <div className={classes.noResultsContainer}>
          {/* <div className={classes.threeDotsContainer}>
            <span /> for spacing
            <div className={classes.subTitle} style={{ marginLeft: 15 }}>
              The E Calculator
            </div>
          </div> */}
          <div className={classes.noResults}>No Results Yet</div>
          <div className={classes.noResultsSmallText}>
            Calculation results will show as you enter the fields.
          </div>
        </div>
      )}
      {showMaxBank && (
        <MaxBankWillLend
          open={showMaxBank}
          handleClose={() => setShowMaxBank(false)}
          storeSliceName={storeSliceName}
          persist={persist}
        />
      )}

      {showIRRScheduleModal && (
        <IRRSchedule
          onClose={() => setShowIRRScheduleModal(false)}
          calculate={calculate}
          storeSliceName={storeSliceName}
        />
      )}
      {showDebtYieldModal && (
        <DebtYieldModal
          open={showDebtYieldModal}
          handleClose={() => setShowDebtYieldModal(false)}
          storeSliceName={storeSliceName}
          persist={persist}
        />
      )}
      {showAmortizationScheduleModal && (
        <AmortizationScheduleModal
          open={showAmortizationScheduleModal}
          onClose={() => setShowAmortizationScheduleModal(false)}
          storeSliceName={storeSliceName}
        />
      )}
    </div>
  );
}

export function SummaryView({ storeSliceName }) {
  const classes = useStyles();
  const { resultsCount, resultsValues, resultsSummary } = useEcalcResults(
    storeSliceName
  );

  const hasResults = !!resultsCount > 0;

  const renderSummaryResult = (
    { summary, results, displayFormat: resultsDisplayFormat },
    index
  ) => {
    if (results !== "" && (!!results || results >= 0)) {
      return (
        <Fragment key={index}>
          <span
            style={{
              opacity: 0.8,
            }}
          >
            {summary}{" "}
          </span>
          <span className={classes.summaryResultValue}>
            {formatDisplay(results, resultsDisplayFormat)}
          </span>
          <span
            style={{
              opacity: 0.8,
            }}
          >
            .{" "}
          </span>
        </Fragment>
      );
    }
  };

  return (
    <div className={classes.listContainer}>
      {hasResults && !!Object.keys(resultsValues).length ? (
        resultsSummary.map((values, index) => (
          <p key={index} className={classes.summaryText}>
            {Object.keys(values).map((key, i) => {
              return Array.isArray(values[key])
                ? values[key].map((value, i2) =>
                    renderSummaryResult(value, `${i}${i2}`)
                  )
                : renderSummaryResult(values[key], i);
            })}
          </p>
        ))
      ) : (
        <div className={classes.noResultsContainer}>
          {/* <div className={classes.threeDotsContainer}>
            <span /> for spacing
            <div className={classes.subTitle} style={{ marginLeft: 15 }}>
              The E Calculator
            </div>
          </div> */}
          <div className={classes.noResults}>No Results Yet</div>
          <div className={classes.noResultsSmallText}>
            Calculation results will show as you enter the fields.
          </div>
        </div>
      )}
    </div>
  );
}

