function formatDisplay(value, displayFormat) {
  if (
    !displayFormat ||
    !value ||
    value.toString().toLowerCase() === "invalid entry"
  ) {
    return value;
  }
  switch (displayFormat) {
    case "dollar":
      return `$${formatDollar(value)}`;
    case "percent":
      return `${value}%`;
    case "times":
      return `${value}x`;
    default:
      return value;
  }
}
