import React, { useState, useEffect, useCallback } from "react";
import clsx from "clsx";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import _flatMap from "lodash/flatMap";
import _sum from "lodash/sum";
import _debounce from "lodash/debounce";
import {
  // Divider,
  TextField,
  InputAdornment,
} from "@material-ui/core";
import { eCalcForms } from "../../../lib";
import {
  setIsDirty,
  eCalcOnChange,
  eCalcOnMultiChange,
  setActiveCalculator,
  addUserEntryMethod,
  addResults,
  removeResults,
  updateItemization,
} from "../../../actions/eCalcForms";
import {
  formatDollar,
  formatPercent,
  entryMethodNames,
  entryMethods,
  numberToPercent,
  parseToNumber,
  noDecimal,
} from "../helpers";
import { calcCapRate } from "./CapRate.logic";
import {
  PotentialIncomeItemized,
  VacancyItemized,
  ExpensesItemized,
} from "../itemizations";
import {
  syncFields as syncVacancyItemizationFields,
  getTotalAmounts as getVacancyItemizationAmounts,
} from "../itemizations/VacancyItemized";
import { ListIcon } from "../../../themes";
import { DisabledInputsMsg } from "../components";
import { useStyles } from "./CapRate.styles";

export function CapRate({ storeSliceName, persist }) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const dirty = useSelector((state) => state[storeSliceName].dirty);
  const capRateValues = useSelector(
    (state) => state[storeSliceName][eCalcForms.CAP_RATE],
    shallowEqual
  );
  const userEntryMethods = useSelector(
    (state) => state[storeSliceName].userEntryMethods,
    shallowEqual
  );
  const incomeItemizationValues = useSelector(
    (state) =>
      state[storeSliceName].itemizations?.[
        eCalcForms.POTENTIAL_GROSS_ITEMIZATION
      ],
    shallowEqual
  );
  const vacancyItemizationValues = useSelector(
    (state) => state[storeSliceName].itemizations?.[eCalcForms.VACANCY],
    shallowEqual
  );
  const vacancyUserItemizationEntryMethods = useSelector(
    (state) =>
      state[storeSliceName].userItemizationEntryMethods?.[eCalcForms.VACANCY],
    shallowEqual
  );
  const expensesItemizationValues = useSelector(
    (state) => state[storeSliceName].itemizations?.[eCalcForms.ANNUAL_EXPENSES],
    shallowEqual
  );
  const {
    purchase_price,
    potential_annual_gross_income,
    potential_monthly_gross_income,
    vacancy_as_dollar,
    vacancy_as_percent,
    annual_expenses,
    monthly_expenses,
    first_year_noi,
  } = capRateValues || {};

  const { vacancy } = userEntryMethods;

  function handleInputChange(event) {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;
    const intValue = parseFloat(value.replace(/[^0-9.]/g, "")); //Strips of non-numeric characters besides decimal
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.CAP_RATE,
        name,
        value: formatInput(name, value),
        persist,
      })
    );
    // need to know which calculator the user is entering for mobile slider
    dispatch(setActiveCalculator({ name: "capRate", persist }));
    typeof syncFields[name] === "function" &&
      syncFields[name]({
        value: intValue,
        dispatch,
        state: capRateValues,
        persist,
      });
    if (!dirty) {
      dispatch(setIsDirty(persist));
    }
  }
  const [effectiveAnnual, setEffectiveAnnual] = useState(0);
  const [effectiveMonthly, setEffectiveMonthly] = useState("");
  const [showIncomeItemization, setShowIncomeItemization] = useState(false);
  const [showVacancyItemization, setShowVacancyItemization] = useState(false);
  const [showExpensesItemization, setShowExpensesItemization] = useState(false);
  const [isIncomeItemized, setIsIncomeItemized] = useState(false);
  const [isVacancyItemized, setIsVacancyItemized] = useState(false);
  const [isExpensesItemized, setIsExpensesItemized] = useState(false);
  const [showIncomeDisabledMsg, setShowIncomeDisabledMsg] = useState(false);
  const [showVacancyDisabledMsg, setShowVacancyDisabledMsg] = useState(false);
  const [showExpensesDisabledMsg, setShowExpensesDisabledMsg] = useState(false);

  useEffect(() => {
    setIsIncomeItemized(checkIfItemizedAnnualMonthly(incomeItemizationValues));
    setShowIncomeDisabledMsg(false);
  }, [incomeItemizationValues]);

  useEffect(() => {
    setIsExpensesItemized(
      checkIfItemizedAnnualMonthly(expensesItemizationValues)
    );
    setShowExpensesDisabledMsg(false);
  }, [expensesItemizationValues]);

  useEffect(() => {
    setIsVacancyItemized(
      checkIfItemizedDollarPercent(vacancyItemizationValues)
    );
    setShowVacancyDisabledMsg(false);
  }, [vacancyItemizationValues]);

  useEffect(() => {
    if (!vacancy_as_dollar || !potential_annual_gross_income) {
      setEffectiveAnnual("");
      setEffectiveMonthly("");
      return;
    }
    const effectiveIncome =
      parseToNumber(potential_annual_gross_income) -
      parseToNumber(vacancy_as_dollar);
    setEffectiveAnnual(formatDollar(effectiveIncome));
    setEffectiveMonthly(formatDollar(effectiveIncome / 12));
  }, [potential_annual_gross_income, vacancy_as_dollar]);

  useEffect(() => {
    let results = "";
    if (effectiveAnnual === 0) return;
    if (effectiveAnnual && annual_expenses) {
      results = formatDollar(
        parseToNumber(effectiveAnnual) - parseToNumber(annual_expenses)
      );
    }
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.CAP_RATE,
        name: "first_year_noi",
        value: results,
        persist,
      })
    );
  }, [effectiveAnnual, annual_expenses, dispatch, persist]);

  useEffect(() => {
    if (potential_annual_gross_income && isVacancyItemized) {
      syncVacancyItemizationWithIncome({
        itemizationValues: vacancyItemizationValues,
        userItemizationEntryMethods: vacancyUserItemizationEntryMethods,
        income: potential_annual_gross_income,
        dispatch,
        persist,
      });
    } else {
      syncVacancy({
        vacancy,
        capRateValues,
        isVacancyItemized,
        dispatch,
        persist,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [potential_annual_gross_income, isVacancyItemized, persist]);

  useEffect(() => {
    if (first_year_noi && purchase_price) {
      const results = calcCapRate({ first_year_noi, purchase_price });
      dispatch(
        addResults({
          name: "cap_rate",
          value: results,
          persist,
        })
      );
    } else {
      dispatch(removeResults({ name: "cap_rate", persist }));
    }
  }, [first_year_noi, purchase_price, dispatch, persist]);

  const openIncomeItemization = useCallback((e) => {
    e.stopPropagation();
    setShowIncomeItemization(true);
  }, []);

  const closeIncomeItemization = useCallback(() => {
    setShowIncomeItemization(false);
  }, []);

  const openVacancyItemization = useCallback((e) => {
    e.stopPropagation();
    setShowVacancyItemization(true);
  }, []);

  const closeVacancyItemization = useCallback(() => {
    setShowVacancyItemization(false);
  }, []);

  const openExpensesItemization = useCallback((e) => {
    e.stopPropagation();
    setShowExpensesItemization(true);
  }, []);

  const closeExpensesItemization = useCallback(() => {
    setShowExpensesItemization(false);
  }, []);

  return (
    <div className={classes.calcContainer}>
      <TextField
        inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
        className={clsx(classes.margin, classes.fullWidthInput)}
        label="Purchase Price:"
        variant="outlined"
        value={purchase_price}
        onChange={handleInputChange}
        name="purchase_price"
        fullWidth
        InputProps={{
          classes: { input: classes.input },
          startAdornment: (
            <InputAdornment
              position="start"
              classes={{ root: classes.inputAdornment }}
            >
              $
            </InputAdornment>
          ),
        }}
        InputLabelProps={{
          classes: {
            root: clsx(classes.inputLabel, classes.fullWidthLabel),
            shrink: "fullInputShrink",
          },
        }}
      />
      {showIncomeDisabledMsg && <DisabledInputsMsg field={"Income"} />}
      <div className={classes.lineInputsContainer}>
        <TextField
          inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Potential Gross Income:"
          variant="outlined"
          value={
            isIncomeItemized
              ? formatDollar(parseToNumber(potential_annual_gross_income))
              : potential_annual_gross_income
          }
          onChange={handleInputChange}
          name="potential_annual_gross_income"
          disabled={isIncomeItemized}
          onClick={
            isIncomeItemized ? () => setShowIncomeDisabledMsg(true) : undefined
          }
          InputProps={{
            classes: { input: classes.input },
            startAdornment: (
              <InputAdornment
                position="start"
                classes={{ root: classes.inputAdornment }}
              >
                $
              </InputAdornment>
            ),
            endAdornment: (
              <InputAdornment position="end">
                <ListIcon
                  className={classes.listIcon}
                  onClick={openIncomeItemization}
                />
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
          }}
        />
        <span className={classes.inputsDivider} />
        <TextField
          inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Monthly Income:"
          variant="outlined"
          value={
            isIncomeItemized
              ? formatDollar(parseToNumber(potential_monthly_gross_income))
              : potential_monthly_gross_income
          }
          onChange={handleInputChange}
          name="potential_monthly_gross_income"
          disabled={isIncomeItemized}
          onClick={
            isIncomeItemized ? () => setShowIncomeDisabledMsg(true) : undefined
          }
          InputProps={{
            classes: { input: classes.input },
            startAdornment: (
              <InputAdornment
                position="start"
                classes={{ root: classes.inputAdornment }}
              >
                $
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
          }}
        />
      </div>
      {showVacancyDisabledMsg && <DisabledInputsMsg field={"Vacancy"} />}
      <div className={classes.lineInputsContainer}>
        <TextField
          inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Vacancy as $:"
          variant="outlined"
          value={
            isVacancyItemized
              ? formatDollar(parseToNumber(vacancy_as_dollar))
              : vacancy_as_dollar
          }
          disabled={isVacancyItemized}
          onClick={
            isVacancyItemized
              ? () => setShowVacancyDisabledMsg(true)
              : undefined
          }
          onChange={handleInputChange}
          name="vacancy_as_dollar"
          InputProps={{
            classes: { input: classes.input },
            startAdornment: (
              <InputAdornment
                position="start"
                classes={{ root: classes.inputAdornment }}
              >
                $
              </InputAdornment>
            ),
            endAdornment: (
              <InputAdornment position="end">
                <ListIcon
                  className={classes.listIcon}
                  onClick={openVacancyItemization}
                />
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
          }}
        />
        <span className={classes.inputsDivider} />
        <TextField
          inputProps={{ inputMode: "decimal" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Vacancy as %:"
          variant="outlined"
          value={vacancy_as_percent}
          disabled={isVacancyItemized}
          onClick={
            isVacancyItemized
              ? () => setShowVacancyDisabledMsg(true)
              : undefined
          }
          onChange={handleInputChange}
          name="vacancy_as_percent"
          InputProps={{
            classes: {
              input: clsx(classes.input, classes.inputWithPadding),
            },
            endAdornment: (
              <InputAdornment
                position="end"
                classes={{ root: classes.inputAdornment }}
              >
                %
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
            shrink: true,
          }}
        />
      </div>
      <div className={classes.lineInputsContainer}>
        <TextField
          className={clsx(classes.margin, classes.smallInput)}
          label="Effective Gross Income:"
          variant="outlined"
          value={effectiveAnnual}
          onChange={() => {}}
          name="effectiveAnnual"
          disabled
          InputProps={{
            classes: { input: classes.input },
            startAdornment: (
              <InputAdornment
                position="start"
                classes={{ root: classes.inputAdornment }}
              >
                $
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
          }}
        />
        <span className={classes.inputsDivider} />
        <TextField
          className={clsx(classes.margin, classes.smallInput)}
          label="Monthly Income:"
          variant="outlined"
          value={effectiveMonthly}
          onChange={() => {}}
          name="effectiveMonthly"
          disabled
          InputProps={{
            classes: { input: classes.input },
            startAdornment: (
              <InputAdornment
                position="start"
                classes={{ root: classes.inputAdornment }}
              >
                $
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
          }}
        />
      </div>
      {showExpensesDisabledMsg && <DisabledInputsMsg field={"Expense"} />}
      <div className={classes.lineInputsContainer}>
        <TextField
          inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Annual Expenses:"
          variant="outlined"
          value={
            isExpensesItemized
              ? formatDollar(parseToNumber(annual_expenses))
              : annual_expenses
          }
          disabled={isExpensesItemized}
          onClick={
            isExpensesItemized
              ? () => setShowExpensesDisabledMsg(true)
              : undefined
          }
          onChange={handleInputChange}
          name="annual_expenses"
          InputProps={{
            classes: { input: classes.input },
            startAdornment: (
              <InputAdornment
                position="start"
                classes={{ root: classes.inputAdornment }}
              >
                $
              </InputAdornment>
            ),
            endAdornment: (
              <InputAdornment position="end">
                <ListIcon
                  className={classes.listIcon}
                  onClick={openExpensesItemization}
                />
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
          }}
        />
        <span className={classes.inputsDivider} />
        <TextField
          inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
          className={clsx(classes.margin, classes.smallInput)}
          label="Monthly Expenses:"
          variant="outlined"
          value={
            isExpensesItemized
              ? formatDollar(parseToNumber(monthly_expenses))
              : monthly_expenses
          }
          disabled={isExpensesItemized}
          onClick={
            isExpensesItemized
              ? () => setShowExpensesDisabledMsg(true)
              : undefined
          }
          onChange={handleInputChange}
          name="monthly_expenses"
          InputProps={{
            classes: { input: classes.input },
            startAdornment: (
              <InputAdornment
                position="start"
                classes={{ root: classes.inputAdornment }}
              >
                $
              </InputAdornment>
            ),
          }}
          InputLabelProps={{
            classes: { root: classes.inputLabel, shrink: "shrink" },
          }}
        />
      </div>
      <TextField
        className={clsx(classes.margin, classes.fullWidthInput)}
        label="First year NOI:"
        variant="outlined"
        value={first_year_noi}
        onChange={() => {}}
        name="first_year_noi"
        fullWidth
        disabled
        InputProps={{
          classes: { input: classes.input },
          startAdornment: (
            <InputAdornment
              position="start"
              classes={{ root: classes.inputAdornment }}
            >
              $
            </InputAdornment>
          ),
        }}
        InputLabelProps={{
          classes: {
            root: clsx(classes.inputLabel, classes.fullWidthLabel),
            shrink: "fullInputShrink",
          },
        }}
      />
      {showIncomeItemization && (
        <PotentialIncomeItemized
          open={showIncomeItemization}
          handleClose={closeIncomeItemization}
          storeSliceName={storeSliceName}
          persist={persist}
        />
      )}
      {showVacancyItemization && (
        <VacancyItemized
          open={showVacancyItemization}
          handleClose={closeVacancyItemization}
          storeSliceName={storeSliceName}
          persist={persist}
        />
      )}
      {showExpensesItemization && (
        <ExpensesItemized
          open={showExpensesItemization}
          handleClose={closeExpensesItemization}
          storeSliceName={storeSliceName}
          persist={persist}
        />
      )}
    </div>
  );
}

const syncFields = {
  potential_annual_gross_income({ value, dispatch, state, persist }) {
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.CAP_RATE,
        name: "potential_monthly_gross_income",
        value: noDecimal(parseToNumber(value) / 12),
        persist,
      })
    );
    dispatch(
      addUserEntryMethod({
        name: entryMethodNames.POTENTIAL_GROSS_INCOME,
        value: entryMethods.ANNUAL,
        persist,
      })
    );
  },
  potential_monthly_gross_income({ value, dispatch, state, persist }) {
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.CAP_RATE,
        name: "potential_annual_gross_income",
        value: noDecimal(parseToNumber(value) * 12),
        persist,
      })
    );
    dispatch(
      addUserEntryMethod({
        name: entryMethodNames.POTENTIAL_GROSS_INCOME,
        value: entryMethods.MONTHLY,
        persist,
      })
    );
  },
  vacancy_as_dollar({ value, dispatch, state, persist }) {
    const { potential_annual_gross_income } = state;
    if (!potential_annual_gross_income) return;
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.CAP_RATE,
        name: "vacancy_as_percent",
        value: formatPercent(
          parseToNumber(value) / parseToNumber(potential_annual_gross_income)
        ),
        persist,
      })
    );
    dispatch(
      addUserEntryMethod({
        name: entryMethodNames.VACANCY,
        value: entryMethods.DOLLAR_AMOUNT,
        persist,
      })
    );
  },
  vacancy_as_percent({ value, dispatch, state, persist }) {
    const { potential_annual_gross_income } = state;
    if (!potential_annual_gross_income) return;
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.CAP_RATE,
        name: "vacancy_as_dollar",
        value: value
          ? noDecimal(
              numberToPercent(value) *
                parseToNumber(state.potential_annual_gross_income)
            )
          : "",
        persist,
      })
    );
    dispatch(
      addUserEntryMethod({
        name: entryMethodNames.VACANCY,
        value: entryMethods.PERCENT_AMOUNT,
        persist,
      })
    );
  },
  annual_expenses({ value, dispatch, state, persist }) {
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.CAP_RATE,
        name: "monthly_expenses",
        value: noDecimal(parseToNumber(value) / 12),
        persist,
      })
    );
    dispatch(
      addUserEntryMethod({
        name: entryMethodNames.EXPENSES,
        value: entryMethods.ANNUAL,
        persist,
      })
    );
  },
  monthly_expenses({ value, dispatch, state, persist }) {
    dispatch(
      eCalcOnChange({
        calculator: eCalcForms.CAP_RATE,
        name: "annual_expenses",
        value: noDecimal(parseToNumber(value) * 12),
        persist,
      })
    );
    dispatch(
      addUserEntryMethod({
        name: entryMethodNames.POTENTIAL_GROSS_INCOME,
        value: entryMethods.MONTHLY,
        persist,
      })
    );
  },
};

function formatInput(name, value) {
  switch (name) {
    case "purchase_price":
    case "potential_annual_gross_income":
    case "potential_monthly_gross_income":
    case "vacancy_as_dollar":
    case "annual_expenses":
    case "monthly_expenses":
    case "first_year_noi":
      return noDecimal(parseToNumber(value));
    default:
      return value;
  }
}

function syncVacancy({ vacancy, capRateValues, dispatch, persist }) {
  const { vacancy_as_dollar, vacancy_as_percent } = capRateValues;
  if (vacancy === entryMethods.DOLLAR_AMOUNT) {
    syncFields.vacancy_as_dollar({
      value: vacancy_as_dollar,
      dispatch,
      state: capRateValues,
      persist,
    });
  } else {
    syncFields.vacancy_as_percent({
      value: vacancy_as_percent,
      dispatch,
      state: capRateValues,
      persist,
    });
  }
}

const syncVacancyItemizationWithIncome = _debounce(
  ({
    itemizationValues = {},
    userItemizationEntryMethods = {},
    income,
    dispatch,
    persist,
  }) => {
    const syncedValues = Object.keys(itemizationValues).reduce((acc, cur) => {
      const entryMethod = userItemizationEntryMethods[cur];
      const syncedFields = syncVacancyItemizationFields({
        nestedKey: entryMethod || entryMethods.DOLLAR_AMOUNT,
        value: itemizationValues[cur][entryMethod] || 0,
        potential_annual_gross_income: income,
      });
      acc[cur] = { ...syncedFields };
      return acc;
    }, {});
    const {
      [entryMethods.DOLLAR_AMOUNT]: dollarAmt,
      [entryMethods.PERCENT_AMOUNT]: percentAmt,
    } = getVacancyItemizationAmounts(syncedValues, income);

    dispatch(
      updateItemization({
        name: eCalcForms.VACANCY,
        value: syncedValues,
        persist,
      })
    );
    dispatch(
      eCalcOnMultiChange({
        calculator: eCalcForms.CAP_RATE,
        valuesObj: {
          vacancy_as_dollar: dollarAmt,
          vacancy_as_percent: percentAmt,
        },
        persist,
      })
    );
  },
  300
);

function checkIfItemizedAnnualMonthly(itemizationValues) {
  const sum = _sum(
    _flatMap(
      itemizationValues,
      (item) =>
        parseToNumber(item[entryMethods.ANNUAL]) +
        parseToNumber(item[entryMethods.MONTHLY])
    )
  );
  return sum > 0;
}

function checkIfItemizedDollarPercent(itemizationValues) {
  const sum = _sum(
    _flatMap(itemizationValues, (item) =>
      parseToNumber(item[entryMethods.DOLLAR_AMOUNT] || 0)
    )
  );
  return sum > 0;
}
