import React, {
  Fragment,
  useState,
  useCallback,
  useEffect,
  useRef,
} from "react";
import useDeepCompareEffect from "use-deep-compare-effect";
import clsx from "clsx";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import mixpanel from "mixpanel-browser";
import debounce from "lodash/debounce";
import {
  useMediaQuery,
  Divider,
  Avatar,
  Accordion,
  AccordionDetails,
  AccordionSummary,
  SwipeableDrawer,
} from "@material-ui/core";
import { MobileResultsTop } from "./components";
import { eCalcForms } from "../../lib";
import {
  CapRate,
  EquityNeeded,
  MortgageDscr,
  CashOnCash,
  Irr,
  EquityWaterfall,
} from "./calculators";
import { SideResults } from "./results";
import {
  DomainIcon,
  AddIcon,
  RemoveIcon,
  MoneyIcon,
  TrendingUpIcon,
  AccountIcon,
  LoopRoundedIcon,
  WaterfallIcon,
  // MoreHoriz,
  useMobile,
} from "../../themes";
import { ECALC_SHOW_SIDE_RESULTS, ECALC_TABLET_VIEW } from "../../lib";
import { useStyles } from "./Calculator.styles";
import { setOpenCalcs } from "../../actions/eCalcForms";

// const Transition = forwardRef(function Transition(props, ref) {
//   return <Slide direction="up" ref={ref} {...props} />;
// });

const debounceTrack = debounce((results = {}, purchasePrice) => {
  const formatedForMix = Object.keys(results).reduce((prev, curr) => {
    prev[curr] = results[curr].results;
    return prev;
  }, {});
  mixpanel.track("ecalc_result", {
    purchase_price: purchasePrice,
    ...formatedForMix,
  });
}, 300);

export function Calculator({
  persist,
  isDealDetails,
  storeSliceName,
  hasToolbar,
}) {
  // "showSideResults" is where the desktop starts to show the results on the side.
  // "isTablet" is the site breakoint for tablet
  const showSideResults = useMediaQuery(ECALC_SHOW_SIDE_RESULTS);
  const isTablet = useMediaQuery(ECALC_TABLET_VIEW);
  const isMobile = useMobile();
  const dispatch = useDispatch();
  const isTabletSlider = !showSideResults && !isMobile;
  const classes = useStyles({
    isTabletSlider,
    isTablet,
    hasToolbar,
    isDealDetails,
  });
  const [showMobileResultsPage, setShowMobileResultsPage] = useState(false);

  const openCalcs = useSelector((state) => state[storeSliceName].openCalcs);

  const addOpenCalc = (calcId) => {
    dispatch(
      setOpenCalcs(
        Array.isArray(openCalcs) ? [...openCalcs, calcId] : [calcId],
        persist
      )
    );
  };

  const removeOpenCalc = (calcId) => {
    if (Array.isArray(openCalcs)) {
      dispatch(
        setOpenCalcs(
          openCalcs.filter((c) => c !== calcId),
          persist
        )
      );
    }
  };

  const resultsValues = useSelector(
    (state) => state[storeSliceName].resultsValues,
    shallowEqual
  );
  const purchase_price = useSelector(
    (state) => state[storeSliceName][eCalcForms.CAP_RATE].purchase_price
  );

  const firstRender = useRef(true);
  // `resultsValues` are causing the useEffect to render too many times.
  // See https://github.com/kentcdodds/use-deep-compare-effect
  useDeepCompareEffect(() => {
    // Don't track when page first loads
    if (firstRender.current) {
      firstRender.current = false;
      return;
    }
    debounceTrack(resultsValues, purchase_price);
  }, [resultsValues]);

  const hasResults = !!Object.keys(resultsValues).length;
  return (
    <div
      className={clsx(classes.ecalcContainer, {
        [classes.tabletEcalcContainer]: isTabletSlider,
        [classes.noRatesContainer]: isDealDetails,
      })}
    >
      {!isMobile && showSideResults && (
        <div>
          <div
            className={clsx(classes.ecalcTitle, {
              [classes.tabletEcalcTitle]: isTabletSlider,
            })}
          >
            eCALC
          </div>
          <div
            className={clsx(classes.subTitle, {
              [classes.tabletSubTitle]: isTabletSlider,
            })}
          >
            The E Calculator
          </div>
          <div className={classes.thirdTitle}>
            A full underwriting experience
          </div>
        </div>
      )}
      {(isMobile || !showSideResults) &&
        Array.isArray(openCalcs) &&
        !openCalcs.length &&
        !hasResults && (
          <div className={classes.mobileTitleContainer}>
            <div
              className={clsx(classes.ecalcTitle, {
                [classes.tabletEcalcTitle]: isTabletSlider,
              })}
            >
              eCALC
            </div>
            <div
              className={clsx(classes.subTitle, {
                [classes.tabletSubTitle]: isTabletSlider,
              })}
            >
              The E Calculator
            </div>
            <div className={classes.thirdTitle}>
              A full underwriting experience
            </div>
          </div>
        )}
      {(isMobile || !showSideResults) &&
        Array.isArray(openCalcs) &&
        !!openCalcs.length &&
        !hasResults && (
          <div className={classes.mobileTitleContainer} style={{ height: 125 }}>
            <div
              className={clsx(classes.ecalcTitle, {
                [classes.tabletEcalcTitle]: isTabletSlider,
              })}
              style={{ margin: 0 }}
            >
              eCALC
            </div>
            <div className={classes.threeDotsContainer}>
              <span /> {/* for spacing */}
              <div
                className={clsx(classes.subTitle, {
                  [classes.tabletSubTitle]: isTabletSlider,
                })}
                style={{ marginLeft: 15 }}
              >
                The E Calculator
              </div>
              <span />{" "}
              {/* for spacing TODO remove when implementing three dots */}
              {/* <MoreHoriz className={classes.threeDots} /> */}
            </div>
            <div className={classes.noResults}>No Results Yet</div>
            <div className={classes.noResultsSmallText}>
              Calculation results will show as you enter the fields.
            </div>
          </div>
        )}
      {(isMobile || !showSideResults) && hasResults && (
        <MobileResultsTop
          setShowMobileResultsPage={setShowMobileResultsPage}
          isTabletSlider={!isMobile}
          storeSliceName={storeSliceName}
          isDealDetails={isDealDetails}
          persist={persist}
          hasToolbar={hasToolbar}
        />
      )}
      <SwipeableDrawer
        anchor="top"
        open={showMobileResultsPage}
        onClose={() => setShowMobileResultsPage(false)}
        onOpen={() => setShowMobileResultsPage(true)}
        disableSwipeToOpen
        disableDiscovery
        classes={{ paperAnchorTop: classes.drawer }}
      >
        <SideResults
          isMobileResults
          setShowMobileResultsPage={setShowMobileResultsPage}
          storeSliceName={storeSliceName}
          persist={persist}
        />
        <div className={classes.mobileClose} />
      </SwipeableDrawer>
      <div
        className={clsx(classes.calculatorsSection, {
          [classes.tabletCalculatorsSection]: isTabletSlider,
          [classes.calculatorsSectionWithResults]: isMobile && hasResults,
          [classes.tabletCalculatorsSectionWithResults]:
            isTabletSlider && hasResults,
          [classes.tabletCalculatorsSectionWithResultsDeal]:
            isTabletSlider && hasResults && isDealDetails,
        })}
      >
        <CalculatorListSection
          title="Cap Rate"
          text="Calculate the cap rate I am paying based on my underwriting"
          Icon={DomainIcon}
          CalcComponent={CapRate}
          calcId="capRate"
          isExpanded={Array.isArray(openCalcs) && openCalcs.includes("capRate")}
          addOpenCalc={addOpenCalc}
          removeOpenCalc={removeOpenCalc}
          storeSliceName={storeSliceName}
          persist={persist}
        />
        <CalculatorListSection
          title="Equity Needed"
          text="Calculate equity needed to close"
          Icon={MoneyIcon}
          CalcComponent={EquityNeeded}
          calcId="equityNeeded"
          isExpanded={
            Array.isArray(openCalcs) && openCalcs.includes("equityNeeded")
          }
          addOpenCalc={addOpenCalc}
          removeOpenCalc={removeOpenCalc}
          storeSliceName={storeSliceName}
          persist={persist}
        />
        <CalculatorListSection
          title="Mortgage and DSCR"
          text="Calculate the mortgage payments and Debt Service Coverage Ratio"
          Icon={AccountIcon}
          CalcComponent={MortgageDscr}
          calcId="mortgageDscr"
          isExpanded={
            Array.isArray(openCalcs) && openCalcs.includes("mortgageDscr")
          }
          addOpenCalc={addOpenCalc}
          removeOpenCalc={removeOpenCalc}
          storeSliceName={storeSliceName}
          persist={persist}
        />
        <CalculatorListSection
          title="Cash-on-Cash"
          text="Calculate the first years cash on cash return"
          Icon={LoopRoundedIcon}
          CalcComponent={CashOnCash}
          calcId="cashOnCash"
          isExpanded={
            Array.isArray(openCalcs) && openCalcs.includes("cashOnCash")
          }
          addOpenCalc={addOpenCalc}
          removeOpenCalc={removeOpenCalc}
          storeSliceName={storeSliceName}
          persist={persist}
        />
        <CalculatorListSection
          title="IRR and Equity Multiplier"
          text="Calculate the main metrics used by Syndicators and Investors"
          Icon={TrendingUpIcon}
          CalcComponent={Irr}
          calcId="irr"
          isExpanded={Array.isArray(openCalcs) && openCalcs.includes("irr")}
          addOpenCalc={addOpenCalc}
          removeOpenCalc={removeOpenCalc}
          storeSliceName={storeSliceName}
          persist={persist}
        />
        <CalculatorListSection
          title="Equity Waterfall"
          text="Calculate the Property IRR based equity waterfall returns to the GP and LP"
          Icon={WaterfallIcon}
          CalcComponent={EquityWaterfall}
          calcId="equityWaterfall"
          isExpanded={
            Array.isArray(openCalcs) && openCalcs.includes("equityWaterfall")
          }
          addOpenCalc={addOpenCalc}
          removeOpenCalc={removeOpenCalc}
          storeSliceName={storeSliceName}
          persist={persist}
        />
      </div>
    </div>
  );
}

function CalculatorListSection({
  Icon,
  CalcComponent,
  title,
  text,
  calcId,
  isExpanded,
  addOpenCalc,
  removeOpenCalc,
  storeSliceName,
  persist,
}) {
  const [expand, setExpand] = useState(isExpanded);
  const classes = useStyles();

  useEffect(() => {
    setExpand(isExpanded);
  }, [isExpanded]);

  const handleToggle = useCallback(() => {
    if (expand) {
      // if currently expanded so we are now closing
      removeOpenCalc(calcId);
    } else {
      mixpanel.track("ecalc_section_opened", { section: title });
      addOpenCalc(calcId);
    }
    setExpand((ex) => !ex);
  }, [expand, removeOpenCalc, addOpenCalc, calcId, title]);

  return (
    <Fragment>
      <Accordion
        expanded={expand}
        onChange={handleToggle}
        // TransitionProps={{ unmountOnExit: true, mountOnEnter: true }}
        className={classes.expansionPanel}
        elevation={0}
      >
        <AccordionSummary
          expandIcon={
            expand ? (
              <RemoveIcon className={classes.addIcon} />
            ) : (
              <AddIcon className={classes.addIcon} />
            )
          }
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <div className={classes.calcListItem}>
            <Avatar className={classes.calcListItemIconContainer}>
              <Icon className={classes.calcListItemIcon} />
            </Avatar>
            <div className={classes.calcListItemDetails}>
              <div className={classes.calcListItemTitle}>{title}</div>
              <div className={classes.calcListItemText}>{text}</div>
            </div>
            {/* <IconButton onClick={handleToggle}>
          <Add className={classes.addIcon} />
        </IconButton> */}
          </div>
        </AccordionSummary>
        <AccordionDetails classes={{ root: classes.expansionPanelDetails }}>
          <CalcComponent storeSliceName={storeSliceName} persist={persist} />
        </AccordionDetails>
      </Accordion>
      <Divider className={classes.divider} />
    </Fragment>
  );
}
